export default function shipGet(api, type = "main")
{
  let main_ship		  = [];
  let vanguard_ship	= [];

  api.forEach((ship) =>
  {
    let main		  = false;
    let vanguard	= false;

    const MAIN_TYPE = [
      "Battleship",
      "Battlecruiser",
      "Aviation Battleship",
      "Aircraft Carrier",
      "Light Carrier",
      "Repair",
      "Monitor"
    ];

    const VANGUARD_TYPE = [
      "Large Cruiser",
      "Heavy Cruiser",
      "Light Cruiser",
      "Destroyer",
      "Munition Ship"
    ];

    if (MAIN_TYPE.find(idx => idx === ship.hullType)) main = true;
    if (VANGUARD_TYPE.find(idx => idx === ship.hullType)) vanguard = true;


    if (ship.id === "30001") ship.names.en = "Hiryuu META";
    if (ship.id === "30002") ship.names.en = "Ark Royal META";

    if (main) main_ship.push(ship);
    if (vanguard) vanguard_ship.push(ship);
  });

  main_ship.sort((a, b) =>
  {
    const NAME_A = a.names.en.toLowerCase();
    const NAME_B = b.names.en.toLowerCase();

    if (NAME_A < NAME_B) return -1;
    if (NAME_A > NAME_B) return 1;

    return 0;
  });

  vanguard_ship.sort((a, b) =>
  {
    const NAME_A = a.names.en.toLowerCase();
    const NAME_B = b.names.en.toLowerCase();

    if (NAME_A < NAME_B) return -1;
    if (NAME_A > NAME_B) return 1;

    return 0;
  });

  if (type === "main")
    return main_ship;

  if (type === "vanguard")
    return vanguard_ship;

  return null;
};
