import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class DPS extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-dps">DPS</th>
        <UncontrolledTooltip placement="top" target="templateHead-dps" delay={0}><b>Damage Per Second</b></UncontrolledTooltip>
      </>
    );
  };
};
