import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Medium extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-medium" className="dps-m">M</th>
        <UncontrolledTooltip placement="top" target="templateHead-medium" delay={0}><b>DPS vs Medium Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
