import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import ASW_img from "../../../../../assets/AL/ASW.png";
import ImgR from "../../../../../components/ImgR.js";

export default class ASW extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-asw"><ImgR src={ASW_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-asw" delay={0}><b>Anti-Submarine Warfare</b></UncontrolledTooltip>
      </>
    );
  };
};
