import PlaneGuns from "../../../../../../../assets/json/PlaneGuns.json";

export default function guns(unit)
{
  const TYPE    = unit
    .replace("T1", "")
    .replace("T2", "")
    .replace("T3", "")
    .replace("T0", "")
    .replace("Cannons", "Cannon")
    .trim();
  let damage    = 0;
  let reload    = 0;
  let fire_rate = 0;
  let tier      = "TN";

  if (unit.includes("T1")) tier = "T1";
  if (unit.includes("T2")) tier = "T2";
  if (unit.includes("T3")) tier = "T3";
  if (unit.includes("T0")) tier = "T0";

  PlaneGuns.forEach((idx) =>
  {
    if (idx.type === TYPE)
    {
      const GUNS_DATA = idx.tier.find(tx => tx.tier === tier);
      if (GUNS_DATA) fire_rate = GUNS_DATA.rldMax;
    };
  });

  if (unit.includes("2 x 30mm Cannon"))
  {
    if (unit.includes("T3")) { damage = 70; reload = 0.97 };
  };

  if (unit.includes("4 x 20mm Cannon"))
  {
    if (unit.includes("T1")) { damage = 68; reload = 0.99 };
    if (unit.includes("T2")) { damage = 74; reload = 0.95 };
    if (unit.includes("T3")) { damage = 80; reload = 0.90 };
    if (unit.includes("T0")) { damage = 86; reload = 0.86 };
  };

  if (unit.includes("2 x 20mm Type-99 Cannon"))
  {
    if (unit.includes("T3")) { damage = 43; reload = 0.90 };
  };

  if (unit.includes("4 x 20mm Type-99 Cannon"))
  {
    if (unit.includes("T1")) { damage = 69; reload = 0.99 };
    if (unit.includes("T2")) { damage = 77; reload = 0.95 };
    if (unit.includes("T3")) { damage = 85; reload = 0.90 };
  };

  if (unit.includes("2 x 20mm Cannon"))
  {
    if (unit.includes("T1")) { damage = 34; reload = 0.71 };
    if (unit.includes("T2")) { damage = 37; reload = 0.68 };
    if (unit.includes("T3")) { damage = 40; reload = 0.65 };
    if (unit.includes("T0")) { damage = 43; reload = 0.65 };
  };

  if (unit.includes("7 x 12.7mm MG"))
  {
    if (unit.includes("T3")) { damage = 69; reload = 0.82 };
  };

  if (unit.includes("6 x 12.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 58; reload = 0.91 };
    if (unit.includes("T2")) { damage = 63; reload = 0.86 };
    if (unit.includes("T3")) { damage = 69; reload = 0.82 };
    if (unit.includes("T0")) { damage = 78; reload = 0.77 };
  };

  if (unit.includes("4 x 12.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 38; reload = 0.70 };
    if (unit.includes("T2")) { damage = 42; reload = 0.67 };
    if (unit.includes("T3")) { damage = 46; reload = 0.64 };
    if (unit.includes("T0")) { damage = 69; reload = 0.60 };
  };

  if (unit.includes("2 x 12.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 27; reload = 0.49 }
    if (unit.includes("T2")) { damage = 28; reload = 0.47 };
    if (unit.includes("T3")) { damage = 32; reload = 0.45 };
  };

  if (unit.includes("8 x 7.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 51; reload = 0.42 };
    if (unit.includes("T2")) { damage = 56; reload = 0.40 };
    if (unit.includes("T3")) { damage = 61; reload = 0.38 };
  };

  if (unit.includes("4 x 7.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 32; reload = 0.42 };
    if (unit.includes("T2")) { damage = 35; reload = 0.40 };
    if (unit.includes("T3")) { damage = 38; reload = 0.38 };
    if (unit.includes("T0")) { damage = 41; reload = 0.38 };
  };

  if (unit.includes("2 x 7.7mm MG"))
  {
    if (unit.includes("T1")) { damage = 20; reload = 0.42 };
    if (unit.includes("T2")) { damage = 22; reload = 0.40 };
    if (unit.includes("T3")) { damage = 26; reload = 0.38 };
  };

  if (unit.includes("1 x 7.7mm MG"))
  {
    if (unit.includes("T0")) { damage = 16; reload = 0.29 };
  };

  if (unit.includes("1 x 13mm MG"))
  {
    if (unit.includes("T1")) { damage = 14; reload = 0.49 };
    if (unit.includes("T2")) { damage = 16; reload = 0.47 };
    if (unit.includes("T3")) { damage = 18; reload = 0.45 };
  };

  if (unit.includes("2 x 7.92mm MG17 MG"))
  {
    if (unit.includes("T2")) { damage = 22; reload = 0.33 };
    if (unit.includes("T3")) { damage = 24; reload = 0.30 };
  };

  if (unit.includes("3 x 7.92mm MG"))
  {
    if (unit.includes("T2")) { damage = 27; reload = 0.38 };
    if (unit.includes("T3")) { damage = 29; reload = 0.36 };
  };

  if (unit.includes("2 x 7.92mm MG"))
  {
    if (unit.includes("T0")) { damage = 22; reload = 0.30 };
  };

  if (unit.includes("1 x 7.92mm MG"))
  {
    if (unit.includes("T0")) { damage = 16; reload = 0.30 };
  };

  if (unit.includes("2 x 13mm MG131 MG"))
  {
    if (unit.includes("T2")) { damage = 28; reload = 0.50 };
    if (unit.includes("T3")) { damage = 32; reload = 0.48 };
  };

  if (unit.includes("1 x 20mm MG151 Cannon"))
  {
    if (unit.includes("T3")) { damage = 20; reload = 0.82 };
  };

  if (unit.includes("3 x 20mm MG151 Cannon"))
  {
    if (unit.includes("T2")) { damage = 55; reload = 0.85 };
    if (unit.includes("T3")) { damage = 60; reload = 0.82 };
  };

  if (unit.includes("2 x 20mm MG FF Cannon"))
  {
    if (unit.includes("T2")) { damage = 38; reload = 0.52 };
    if (unit.includes("T3")) { damage = 41; reload = 0.50 };
  };

  if (unit.includes("2 x 13mm Cannon"))
  {
    if (unit.includes("T3")) { damage = 27; reload = 0.47 };
  };

  if (damage === 0 || reload === 0 || fire_rate === 0) console.log(unit);
  return ({ damage: damage, reload: reload, firerate: fire_rate });
};
