import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class AntiAirBurst extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-antiairburst">AA Burst</th>
        <UncontrolledTooltip placement="top" target="templateHead-antiairburst" delay={0}><b>Anti-Air Burst</b></UncontrolledTooltip>
      </>
    );
  };
};
