import React, { Component } from "react";
import PropTypes from "prop-types";
import { Navigate } from "react-router-dom";

import ProgressLoad     from "../../components/ProgressLoad.js";
import EquipmentProfile from "./EquipmentProfile.js";

const propTypes = { paramsId: PropTypes.string };

export default class EquipmentFetch extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      azurAPI: []
    };
  };

  componentDidMount()
  {
    this.mounted = true;
    Promise.all([
      fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
        .then(res => res.json())
    ]).then(
      ([azurAPI]) =>
      {
        if (this.mounted)
          this.setState({ isLoaded: true, azurAPI: azurAPI });
      },
      (error) =>
      {
        this.setState({ isLoaded: true, error });
      }
    );
  };

  componentWillUnmount()
  {
    this.mounted = false;
  };

  render()
  {
    const { error, isLoaded, azurAPI } = this.state;
    if (error)
      return <div>Error: {error.message}</div>;
    else if (!isLoaded)
      return <ProgressLoad text="Loading..." />;

    const EQUIPMENT_ID   = this.props.paramsId;
    const EQUIPMENT_DATA = azurAPI.find(equip => equip.id.toLowerCase().replace(/\s+/g, "_") === EQUIPMENT_ID.toLowerCase());
    if (EQUIPMENT_DATA === undefined) return <Navigate to="/equipment" />;

    return <EquipmentProfile equipmentData={EQUIPMENT_DATA} />;
  };
};

EquipmentFetch.propTypes = propTypes;
