import React, { Component } from "react";
import PropTypes from "prop-types";

import ImgR from "../../../../../components/ImgR.js";

// function
import pointRound from "../../../../../components/pointRound.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array
};

export default class AntiSubmarine extends Component
{
  render()
  {
    const NAVIGATE		   = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;

    return (
      <tbody>
        {EQUIPMENT_LIST.map((equip, key) =>
        {
          const ICON = equip.image;
          const NAME = equip.names.en;

          const EQUIPMENT_RARITY = equip.highest_rarity;
          const EQUIPMENT_LINK   = encodeURIComponent(NAME.replace(/\s+/g, "_"));

          const STATS = equip.stats;

          let asw      = "";
          let accuracy = "";
          let damage   = "";
          let reload   = "";
          let dps      = "";
          let range    = "";
          let radius   = "";

          // Anti Submarine Warfare
          if (STATS.antisubmarineWarfare)
          {
            asw = STATS.antisubmarineWarfare.formatted;
            if (STATS.antisubmarineWarfare.type === "min_mid_max")
              asw = STATS.antisubmarineWarfare.mid;
          };

          // Accuracy
          if (STATS.accuracy)
            accuracy = STATS.accuracy.mid;

          // Damage
          if (STATS.damage)
            damage = STATS.damage.max;

          // Reload
          if (STATS.rateOfFire)
            reload = STATS.rateOfFire.max;

          // Damage Per Second
          if (damage !== "" && reload !== "")
            dps = pointRound(parseInt(damage) / parseFloat(reload));

          // Range
          if (STATS.range)
            range = STATS.range.formatted;

          // Radius
          if (STATS.areaOfAttack)
            radius = STATS.areaOfAttack.formatted;

          const STARS = equip.stars.stars;
          const TYPE  = equip.type.name;
          const NOTES = equip.misc.notes;

          return (
            <tr key={key} onClick={() => NAVIGATE(EQUIPMENT_LINK)}>
              <td>
                <div className={"m-auto equipment-rarity-" + EQUIPMENT_RARITY}><ImgR src={ICON} className="p-10" /></div>
              </td>
              <td>{NAME}</td>
              <td>{STARS}</td>
              <td>{asw}</td>
              <td>{accuracy}</td>
              <td>{damage}</td>
              <td>{reload}</td>
              <td>{dps}</td>
              <td>{range}</td>
              <td>{radius}</td>
              <td>{TYPE}</td>
              <td>{NOTES}</td>
            </tr>
          );
        })}
      </tbody>
    );
  };
};

AntiSubmarine.propTypes = propTypes;
