export default function hullGet(type)
{
  if (type === "Destroyer") return "destroyer";

  if (type === "Light Cruiser") return "lightCruiser";

  if (type === "Heavy Cruiser") return "heavyCruiser";

  if (type === "Monitor") return "monitor";

  if (type === "Large Cruiser") return "largeCruiser";

  if (type === "Battleship") return "battleship";

  if (type === "Aviation Battleship") return "aviationBattleship";

  if (type === "Aircraft Carrier") return "aircraftCarrier";

  if (type === "Light Carrier") return "lightCarrier";

  if (type === "Light Aircraft Carrier") return "lightCarrier";

  if (type === "Repair") return "repairShip";

  if (type === "Munition Ship") return "munitionShip";

  if (type === "Submarine") return "submarine";

  if (type === "Submarine Carrier") return "submarineCarrier";

  return null;
};
