function constructor(number, { midpoint = 5, decimal = 2 })
{
  let multiplier = 1;
  for (let i = 0; i < decimal; i++) multiplier *= 10;

  const NUMBER_MULTIPLY = number * multiplier;
  const DOT_INDEX = String(NUMBER_MULTIPLY).indexOf(".");

  const ROUNDING_NUMBER = Number(NUMBER_MULTIPLY.toString().slice(DOT_INDEX + 1, DOT_INDEX + 2));

  if (ROUNDING_NUMBER < midpoint)
    return Math.floor(NUMBER_MULTIPLY) / 100;

  return Math.ceil(NUMBER_MULTIPLY) / 100;
};

export default function pointRound(number, option = {})
{
  return constructor(number, option);
};
