import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Ammo extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-ammo">Ammo</th>
        <UncontrolledTooltip placement="top" target="templateHead-ammo" delay={0}><b>Ammo Type</b></UncontrolledTooltip>
      </>
    );
  };
};
