import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Radius extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-radius">AoE Radius</th>
        <UncontrolledTooltip placement="top" target="templateHead-radius" delay={0}><b>AoE Radius</b></UncontrolledTooltip>
      </>
    );
  };
};
