import React, { Component } from "react";
import PropTypes from "prop-types";
import { Modal, ModalBody, Table } from "reactstrap";

// function
import rarityGet from "../../../components/rarityGet.js";

// core components
import ModalHeader from "./FleetModal/ModalHeader.js";
import Ship        from "./FleetModal/Ship.js";
import Reload      from "./FleetModal/Reload.js";
import Equipment   from "./FleetModal/Equipment.js";

// function
import fleetDOM      from "./FleetModal/fleetDOM.js";
import getShipReload from "./FleetModal/getShipReload.js";

const propTypes = {
  azurapi: PropTypes.any,
  banner: PropTypes.any,
  stats: PropTypes.any,
  gear: PropTypes.any,
  modalData: PropTypes.object,
  modalOpen: PropTypes.bool,
  slot: PropTypes.number,
  openGearModal: PropTypes.func,
  gearClose: PropTypes.func,
  gearData: PropTypes.object,
  gearSlot: PropTypes.object,
  modalClose: PropTypes.func,
  shipChange: PropTypes.func
};

export default class FleetModal extends Component
{
  render()
  {
    const AZURAPI = this.props.azurapi;
    const BANNER  = this.props.banner;
    const STATS   = this.props.stats;
    const GEAR    = this.props.gear;

    const MODAL_DATA = this.props.modalData;
    const MODAL_OPEN = this.props.modalOpen;
    const SLOT       = this.props.slot;

    const GEAR_DATA = this.props.gearData;
    const GEAR_SLOT = this.props.gearSlot;

    let ship_slot = MODAL_DATA.slot1;
    let gear_slot = GEAR_DATA.slot1;

    if (SLOT === 1) { ship_slot = MODAL_DATA.slot1; gear_slot = GEAR_DATA.slot1 };
    if (SLOT === 2) { ship_slot = MODAL_DATA.slot2; gear_slot = GEAR_DATA.slot2 };
    if (SLOT === 3) { ship_slot = MODAL_DATA.slot3; gear_slot = GEAR_DATA.slot3 };

    // current slot extract
    const SHIP_API  = ship_slot.azurapi;
    let ship_id     = ship_slot.id;
    let ship_name   = ship_slot.name;
    let ship_banner = ship_slot.banner;
    let ship_type   = ship_slot.type;
    let ship_kai    = ship_slot.kai;
    let ship_level  = ship_slot.level;
    let ship_oath   = ship_slot.oath;
    let ship_rarity = ship_slot.rarity;
    let ship_reload = ship_slot.reload;
    let ship_tech   = ship_slot.tech;
    let ship_cat    = ship_slot.cat;

    let data_build = {
      azurapi: SHIP_API,
      id: ship_id,
      name: ship_name,
      banner: ship_banner,
      type: ship_type,
      kai: ship_kai,
      level: ship_level,
      oath: ship_oath,
      rarity: ship_rarity,
      reload: ship_reload,
      tech: ship_tech,
      cat: ship_cat
    };

    // current slot
    let final_reload = ship_reload + ship_tech + ship_cat;

    // input event getShipOption
    const getShipOption = (event) =>
    {
      const OPTION_VALUE = event.target.value;
      const SHIP_DATA    = AZURAPI.find(idx => idx.id === OPTION_VALUE);
      const BANNER_DATA  = BANNER.find(idx => idx.id === OPTION_VALUE);

      if (BANNER_DATA) ship_banner = BANNER_DATA.skins[0].banner;

      let primary_type;
      let secondary_type;
      let tertiary_type;

      let primary_max;
      let secondary_max;
      let tertiary_max;

      let primary_efficiency;
      let secondary_efficiency;
      let tertiary_efficiency;

      if (SHIP_DATA)
      {
        ship_id     = SHIP_DATA.id;
        ship_name   = SHIP_DATA.names.en;
        ship_type   = SHIP_DATA.hullType;
        ship_rarity = SHIP_DATA.rarity;
        ship_reload = parseInt(SHIP_DATA.stats.level125.reload);

        primary_type   = SHIP_DATA.slots[0].type;
        secondary_type = SHIP_DATA.slots[1].type;
        tertiary_type  = SHIP_DATA.slots[2].type;

        primary_max   = SHIP_DATA.slots[0].max;
        secondary_max = SHIP_DATA.slots[1].max;
        tertiary_max  = SHIP_DATA.slots[2].max;

        primary_efficiency   = SHIP_DATA.slots[0].maxEfficiency;
        secondary_efficiency = SHIP_DATA.slots[1].maxEfficiency;
        tertiary_efficiency  = SHIP_DATA.slots[2].maxEfficiency;

        if (SHIP_DATA.retrofit)
        {
          ship_kai = { status: true, availability: true };

          if (BANNER_DATA && BANNER_DATA.skins[1].name === "Retrofit")
            ship_banner = BANNER_DATA.skins[1].banner;

          ship_rarity = rarityGet(ship_rarity, 3);
          ship_reload = parseInt(SHIP_DATA.stats.level125Retrofit.reload);
        }
        else
        {
          ship_kai = { status: false, availability: false };
        };
      }
      else
      {
        ship_id     = "000";
        ship_name   = "Unknown";
        ship_banner = "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png";
        ship_type   = "Unknown-Type";
        ship_kai    = { status: false, availability: false };
        ship_rarity = "Unknown-Rarity";
        ship_reload = 0;
        ship_tech   = 0;
        ship_cat    = 0;

        primary_type   = "Primary";
        secondary_type = "Secondary";
        tertiary_type  = "Tertiary";

        primary_max   = 0;
        secondary_max = 0;
        tertiary_max  = 0;

        primary_efficiency   = 0;
        secondary_efficiency = 0;
        tertiary_efficiency  = 0;
      };

      ship_level = "125";
      ship_oath  = false;

      // build
      data_build = {
        azurapi: SHIP_DATA,
        id: ship_id,
        name: ship_name,
        banner: ship_banner,
        type: ship_type,
        kai: ship_kai,
        level: ship_level,
        oath: ship_oath,
        rarity: ship_rarity,
        reload: ship_reload,
        tech: ship_tech,
        cat: ship_cat
      };

      gear_slot = {
        primary: "000",
        secondary: "000",
        tertiary: "000",
        auxiliary_1: "000",
        auxiliary_2: "000"
      };

      const FLEET_DOM = {
        ...data_build,
        primary_type,
        secondary_type,
        tertiary_type,
        primary_max,
        secondary_max,
        tertiary_max,
        primary_efficiency,
        secondary_efficiency,
        tertiary_efficiency
      };

      fleetDOM(FLEET_DOM);
      this.props.shipChange(data_build, gear_slot, SLOT);
    };

    // input event getLevelOption => extends #level-select
    const getLevelOption = (event) =>
    {
      ship_level = event.target.value;
      data_build.level = ship_level;

      ship_reload = getShipReload(data_build, STATS);
      data_build.reload = parseInt(ship_reload);
      document.getElementById("reload-base").value = ship_reload;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event getRetrofitOption => extends #retrofit-select
    const getRetrofitOption = (event) =>
    {
      const OPTION_VALUE = event.target.value;
      const BANNER_DATA  = BANNER.find(idx => idx.id === ship_id);

      // retrofit true
      if (OPTION_VALUE === "1")
      {
        if (data_build.kai.status) return;

        data_build.kai.status = true;
        if (BANNER_DATA && BANNER_DATA.skins[1].name === "Retrofit")
        {
          ship_banner = BANNER_DATA.skins[1].banner;
          data_build.banner = ship_banner;
        };

        ship_rarity = rarityGet(ship_rarity, 3);
        data_build.rarity = ship_rarity;

        ship_reload = getShipReload(data_build, STATS);
        data_build.reload = parseInt(ship_reload);

        document.getElementById("slot-ship-name").append(" (Retrofit)");
      };

      // retrofit false
      if (OPTION_VALUE === "0")
      {
        if (!data_build.kai.status) return;

        data_build.kai.status = false;
        if (BANNER_DATA)
        {
          ship_banner = BANNER_DATA.skins[0].banner;
          data_build.banner = ship_banner;
        };

        ship_rarity = rarityGet(ship_rarity, 4);
        data_build.rarity = ship_rarity;

        ship_reload = getShipReload(data_build, STATS);
        data_build.reload = ship_reload;

        document.getElementById("slot-ship-name").innerHTML = ship_name;
      };

      document.getElementById("reload-base").value = ship_reload;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
      document.getElementById("slot-ship-banner").src = ship_banner;
      document.getElementById("slot-ship-rarity").innerHTML = ship_rarity;
    };

    // input event getOathOption => extends #oath-select
    const getOathOption = (event) =>
    {
      if (event.target.value === "1") ship_oath = true;
      if (event.target.value === "0") ship_oath = false;
      data_build.oath = ship_oath;

      ship_reload = getShipReload(data_build, STATS);
      data_build.reload = parseInt(ship_reload);
      document.getElementById("reload-base").value = ship_reload;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event inputReload => extends #reload-base
    const inputReload = (event) =>
    {
      ship_reload = parseInt(event.target.value);
      data_build.reload = ship_reload;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event inputTech => extends #reload-tech
    const inputTech = (event) =>
    {
      ship_tech = parseInt(event.target.value);
      data_build.tech = ship_tech;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // input event inputCat => extends #reload-cat
    const inputCat = (event) =>
    {
      ship_cat = parseInt(event.target.value);
      data_build.cat = ship_cat;
      document.getElementById("reload-final").value = parseInt(ship_reload) + parseInt(ship_tech) + parseInt(ship_cat);
    };

    // close modal output
    const modalClose = () => this.props.modalClose(data_build, SLOT);

    const openGearModal = (idx) =>
    {
      this.props.openGearModal({ slot: idx.slot, id: idx.id, type: idx.type, kai: idx.kai });
      this.props.modalClose(data_build, SLOT, true);
    };

    return (
      <Modal isOpen={MODAL_OPEN} toggle={modalClose} size="xl" className="pb-5 dark">
        <ModalHeader toggle={modalClose} id="slot-ship-name" banner={ship_banner} name={ship_name} kai={ship_kai} />
        <ModalBody className="text-center align-middle">
          <Ship
            rarity={ship_rarity}
            type={ship_type}
            azurapi={AZURAPI}
            shipId={ship_id}
            getShipOption={getShipOption}
            level={ship_level}
            getLevelOption={getLevelOption}
            retrofit={ship_kai}
            getRetrofitOption={getRetrofitOption}
            oath={ship_oath}
            getOathOption={getOathOption}
          />
          <Table className="fleetModalTable">
            <Reload
              inputReload={inputReload}
              inputTech={inputTech}
              inputCat={inputCat}
              reload={ship_reload}
              tech={ship_tech}
              cat={ship_cat}
              finalReload={final_reload}
            />
          </Table>
          <Table dark className="fleetModalTable">
            <Equipment
              dataBuild={data_build}
              gear={GEAR}
              gearData={gear_slot}
              gearSlot={GEAR_SLOT}
              openGearModal={openGearModal}
            />
          </Table>
        </ModalBody>
      </Modal>
    );
  };
};

FleetModal.propTypes = propTypes;
