import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Round extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-round">Rnd</th>
        <UncontrolledTooltip placement="top" target="templateHead-round" delay={0}><b>Round</b></UncontrolledTooltip>
      </>
    );
  };
};
