import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = { skills: PropTypes.array };

export default class ShipSkills extends Component
{
  render()
  {
    // Skills               => {multiple array[]} (type: string)
    // Skills[Array]        => {icon, names, description, color} (type: string)
    // Skills[Array].names  => {en, cn, jp} (type: string)
    const SKILLS = this.props.skills;

    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th colSpan="2">Skill</th>
            </tr>
          </thead>
          <tbody>
            {SKILLS.map((skill, key) =>
            {
              const NAME = skill.names.en;
              const ICON = skill.icon;
              const DESC = skill.description;
              const TYPE = skill.color;
              return (
                <tr key={key} className={"skill-" + TYPE}>
                  <th>
                    <img src={ICON} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
                    <br />
                    {NAME}
                  </th>
                  <th>
                    {DESC.split("\n").map((line, i) => (<span key={i}>{line}<br/></span>))}
                  </th>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    );
  };
};

ShipSkills.propTypes = propTypes;
