import rarityGet from "./rarityGet.js";

export default function highestRarity(gear)
{
  let equipment_list = [];
  gear.forEach((api) =>
  {
    let current_gear     = api;
    let rarity_container = [];
    let tier_container   = [];

    api.tiers.forEach((idx) =>
    {
      if (idx)
      {
        rarity_container.push(idx.rarity);
        tier_container.push(idx);
      };
    });
    let equipment_rarity = 0;

    rarity_container.forEach((x) =>
    {
      if (x === "Normal" && equipment_rarity < 1)
        equipment_rarity = 1;

      if (x === "Rare" && equipment_rarity < 2)
        equipment_rarity = 2;

      if (x === "Elite" && equipment_rarity < 3)
        equipment_rarity = 3;

      if (x === "Super Rare" && equipment_rarity < 4)
        equipment_rarity = 4;

      if (x === "Ultra Rare" && equipment_rarity < 5)
        equipment_rarity = 5;
    });

    let equipment_stats = {};
    tier_container.forEach((idx) =>
    {
      if (rarityGet(idx.rarity, 2) === equipment_rarity) equipment_stats = idx;
    });

    const REBUILD = {
      fits: current_gear.fits,
      image: current_gear.image,
      misc: current_gear.misc,
      nationality: current_gear.nationality,
      type: current_gear.type,
      id: current_gear.id,
      wikiUrl: current_gear.wikiUrl,
      category: current_gear.category,
      names: current_gear.names
    };

    current_gear = { ...REBUILD, ...equipment_stats, highest_rarity: equipment_rarity };

    if (equipment_rarity > 1) equipment_list.push(current_gear);
  });

  return equipment_list;
};
