import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

// extend Components
import ProgressLoad from "../components/ProgressLoad.js";

// Core Components
import FleetBuild from "./Fleet/FleetBuild.js";

function Fleet()
{
  const { pathname }      = useLocation();
  const [error, setError] = useState(null);

  // azurapi state
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI]   = useState([]);

  // shipbanner state
  const [bannerIsLoaded, setBannerIsLoaded] = useState(false);
  const [shipBanner, setShipBanner]         = useState([]);

  // stats state
  const [statsIsLoaded, setStatsIsLoaded] = useState(false);
  const [stats, setStats]                 = useState([]);

  // gear state
  const [gearIsLoaded, setGearIsLoaded] = useState(false);
  const [gear, setGear]                 = useState([]);

  // equip state
  const [equipIsLoaded, setEquipIsLoaded] = useState(false);
  const [equip, setEquip]                 = useState([]);

  useEffect(() =>
  {
    if (pathname) window.scrollTo(0, 0);
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
      .then(res => res.json())
      .then(
        (result) => { setIsLoaded(true); setAzurAPI(result) },
        (errors) => { setIsLoaded(true); setError(errors) }
      );
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
      .then(res => res.json())
      .then(
        (result) => { setBannerIsLoaded(true); setShipBanner(result) },
        (errors) => { setBannerIsLoaded(true); setError(errors) }
      );
    fetch("https://raw.githubusercontent.com/Asterisk-api/AzurLane/main/statistics.json")
      .then(res => res.json())
      .then(
        (result) => { setStatsIsLoaded(true); setStats(result) },
        (errors) => { setStatsIsLoaded(true); setError(errors) }
      );
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/equipments.json")
      .then(res => res.json())
      .then(
        (result) => { setGearIsLoaded(true); setGear(result) },
        (errors) => { setGearIsLoaded(true); setError(errors) }
      );
    fetch("https://raw.githubusercontent.com/Asterisk-api/AzurLane/main/equipable.json")
      .then(res => res.json())
      .then(
        (result) => { setEquipIsLoaded(true); setEquip(result) },
        (errors) => { setEquipIsLoaded(true); setError(errors) }
      );
  }, [pathname]);

  // slot state
  const [ slot, setSlot ] = useState({ slot: 0 });

  const [ modal, setModal ]         = useState(false);
  const [ modalData, setModalData ] = useState({
    slot1: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    },
    slot2: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    },
    slot3: {
      azurapi: null,
      id: "000",
      name: "Unknown",
      banner: "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/images/skins/000/Default/Banner.png",
      type: "Unknown-Type",
      kai: { status: false, availability: false },
      level: "125",
      oath: false,
      rarity: "Unknown-Rarity",
      reload: 0,
      tech: 0,
      cat: 0
    }
  });

  const openShipModal = (idx) =>
  {
    setSlot({ slot: idx.slot });
    setModal(true);
  };

  const closeModal = (idx, shipSlot, modalStatus = false) =>
  {
    let slot_1 = modalData.slot1;
    let slot_2 = modalData.slot2;
    let slot_3 = modalData.slot3;

    if (shipSlot === 1) slot_1 = idx;
    if (shipSlot === 2) slot_2 = idx;
    if (shipSlot === 3) slot_3 = idx;

    setModalData({ slot1: slot_1, slot2: slot_2, slot3: slot_3 });

    setModal(modalStatus);
  };

  const [ gearSlot, setGearSlot ]   = useState({ slot: 0, id: "000", type: "000", kai: false });
  const [ gearModal, setGearModal ] = useState(false);
  const [ gearData, setGearData ]   = useState({
    slot1: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    },
    slot2: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    },
    slot3: {
      primary: "000",
      secondary: "000",
      tertiary: "000",
      auxiliary_1: "000",
      auxiliary_2: "000"
    }
  });

  const openGearModal = (idx) =>
  {
    setGearSlot({ slot: idx.slot, id: idx.id, type: idx.type, kai: idx.kai });
    setGearModal(true);
  };

  const gearClose = (idx, shipSlot, gearModalStatus = false) =>
  {
    let slot_1 = gearData.slot1;
    let slot_2 = gearData.slot2;
    let slot_3 = gearData.slot3;

    if (shipSlot === 1) slot_1 = idx;
    if (shipSlot === 2) slot_2 = idx;
    if (shipSlot === 3) slot_3 = idx;

    setGearData({ slot1: slot_1, slot2: slot_2, slot3: slot_3 });
    setGearModal(gearModalStatus);
  };

  const shipChange = (shipData, equipData, shipSlot) =>
  {
    let ship_data_1 = modalData.slot1;
    let ship_data_2 = modalData.slot2;
    let ship_data_3 = modalData.slot3;

    let equip_data_1 = gearData.slot1;
    let equip_data_2 = gearData.slot2;
    let equip_data_3 = gearData.slot3;

    if (shipSlot === 1) { ship_data_1 = shipData; equip_data_1 = equipData };
    if (shipSlot === 2) { ship_data_2 = shipData; equip_data_2 = equipData };
    if (shipSlot === 3) { ship_data_3 = shipData; equip_data_3 = equipData };

    setModalData({ slot1: ship_data_1, slot2: ship_data_2, slot3: ship_data_3 });
    setGearData({ slot1: equip_data_1, slot2: equip_data_2, slot3: equip_data_3 });
  };

  if (error)
    return <div>Error: {error.message}</div>;
  else if (!isLoaded)
    return (<ProgressLoad text="Loading AzurAPI..." />);
  else if (!bannerIsLoaded)
    return (<ProgressLoad text="Loading ShipBanner..." />);
  else if (!statsIsLoaded)
    return (<ProgressLoad text="Loading Stats..." />);
  else if (!gearIsLoaded)
    return (<ProgressLoad text="Loading Equipments..." />);
  else if (!equipIsLoaded)
    return (<ProgressLoad text="Loading Equipable..." />);

  return (
    <FleetBuild
      azurAPI={azurAPI}
      shipBanner={shipBanner}
      stats={stats}
      gear={gear}
      equip={equip}
      openShipModal={openShipModal}
      modalOpen={modal}
      modalClose={closeModal}
      modalData={modalData}
      openGearModal={openGearModal}
      slot={slot.slot}
      gearData={gearData}
      gearOpen={gearModal}
      gearClose={gearClose}
      gearSlot={gearSlot}
      shipChange={shipChange}
    />
  );
}

export default Fleet;
