import React, { Component } from "react";
import PropTypes from "prop-types";

// Components
import ImgR from "../../../../../components/ImgR.js";

// Functions
import factionClass from "../../../../../components/factionClass.js";
import dps from "./Formula/dps.js";
import antiAir from "./Formula/antiAir.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array,
  type: PropTypes.string
};

export default class Plane extends Component
{
  render()
  {
    const NAVIGATE = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;
    const EQUIPMENT_TYPE = this.props.type;

    let gear_type = "NONE";
    if (EQUIPMENT_TYPE === "fighter_planes") gear_type = "F";
    if (EQUIPMENT_TYPE === "dive_bomber_planes") gear_type = "DB";
    if (EQUIPMENT_TYPE === "torpedo_bomber_planes") gear_type = "TB";
    if (EQUIPMENT_TYPE === "seaplanes") gear_type = "SP";

    return (
      <tbody>
        {EQUIPMENT_LIST.map((equip, key) =>
        {
          const ICON = equip.image;
          const NAME = equip.names.en;

          const FACTION       = equip.nationality;
          const FACTION_CLASS = factionClass(FACTION);

          const EQUIPMENT_RARITY = equip.highest_rarity;
          const EQUIPMENT_LINK   = encodeURIComponent(NAME.replace(/\s+/g, "_"));
          const STATS = equip.stats;

          let aviation  = 0;

          if (STATS.aviation) aviation = STATS.aviation.formatted;

          let ordnance_list = [];
          let gun_list      = [];

          if (STATS.ordnance) STATS.ordnance.stats.forEach((idx) => ordnance_list.push(idx.formatted));
          if (STATS.aAGuns) STATS.aAGuns.stats.forEach((idx) => gun_list.push(idx.formatted));

          const RATE_OF_FIRE   = STATS.rateOfFire.max;
          const SPEED          = STATS.speed.formatted;
          const CRASH_DAMAGE   = STATS.crashDamage.formatted;

          const LIGHT  = dps(STATS, "Light", gear_type);
          const MEDIUM = dps(STATS, "Medium", gear_type);
          const HEAVY  = dps(STATS, "Heavy", gear_type);

          const AA_DMG   = antiAir(STATS, gear_type, NAME);
          const AA_DPS   = AA_DMG.dps;
          const AA_BURST = AA_DMG.burst;

          let damage = <></>;
          if (gear_type === "TB") damage = <td>{STATS.damage.max}</td>;

          return (
            <tr key={key} onClick={() => NAVIGATE(EQUIPMENT_LINK)}>
              <td>
                <div className={"m-auto equipment-rarity-" + EQUIPMENT_RARITY}><ImgR src={ICON} className="p-10" /></div>
              </td>
              <td>{NAME}</td>
              <td>{aviation}</td>
              <td>{ordnance_list.map((idx) => <p key={key + idx}>{idx}</p>)}</td>
              <td>{gun_list.map((idx) => <p key={key + idx}>{idx}</p>)}</td>
              {damage}
              <td>{RATE_OF_FIRE}</td>
              <td className="dps-l">{LIGHT}</td>
              <td className="dps-m">{MEDIUM}</td>
              <td className="dps-h">{HEAVY}</td>
              <td>{AA_DPS}</td>
              <td>{AA_BURST}</td>
              <td>{SPEED}</td>
              <td>{CRASH_DAMAGE}</td>
              <td className={FACTION_CLASS + " faction-background"}>{FACTION}</td>
            </tr>
          );
        })}
      </tbody>
    );
  };
};

Plane.propTypes = propTypes;
