import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import Hit_img from "../../../../../assets/AL/Hit.png";
import ImgR from "../../../../../components/ImgR.js";

export default class Hit extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-hit"><ImgR src={Hit_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-hit" delay={0}><b>Hit</b></UncontrolledTooltip>
      </>
    );
  };
};
