export default function fleetDOM(ship)
{
  const BANNER = ship.banner;
  const NAME   = ship.name;
  const TYPE   = ship.type;
  const RARITY = ship.rarity;

  const PRIMARY_TYPE   = ship.primary_type;
  const SECONDARY_TYPE = ship.secondary_type;
  const TERTIARY_TYPE  = ship.tertiary_type;

  const PRIMARY_MAX   = ship.primary_max;
  const SECONDARY_MAX = ship.secondary_max;
  const TERTIARY_MAX  = ship.tertiary_max;

  const PRIMARY_EFFICIENCY   = ship.primary_efficiency;
  const SECONDARY_EFFICIENCY = ship.secondary_efficiency;
  const TERTIARY_EFFICIENCY  = ship.tertiary_efficiency;

  const SHIP_KAI    = ship.kai;
  const SHIP_RELOAD = ship.reload;
  const SHIP_TECH   = ship.tech;
  const SHIP_CAT    = ship.cat;

  document.getElementById("slot-ship-banner").src = BANNER;
  document.getElementById("slot-ship-name").innerHTML = NAME;
  document.getElementById("slot-ship-type").innerHTML = TYPE;
  document.getElementById("slot-ship-rarity").innerHTML = RARITY;

  document.getElementById("equipment-type-1").innerHTML = PRIMARY_TYPE;
  document.getElementById("equipment-type-2").innerHTML = SECONDARY_TYPE;
  document.getElementById("equipment-type-3").innerHTML = TERTIARY_TYPE;

  document.getElementById("equipment-max-1").innerHTML = PRIMARY_MAX;
  document.getElementById("equipment-max-2").innerHTML = SECONDARY_MAX;
  document.getElementById("equipment-max-3").innerHTML = TERTIARY_MAX;

  document.getElementById("equipment-efficiency-1").innerHTML = PRIMARY_EFFICIENCY + "%";
  document.getElementById("equipment-efficiency-2").innerHTML = SECONDARY_EFFICIENCY + "%";
  document.getElementById("equipment-efficiency-3").innerHTML = TERTIARY_EFFICIENCY + "%";

  if (SHIP_KAI.status) document.getElementById("slot-ship-name").append(" (Retrofit)");

  document.getElementById("reload-base").value  = SHIP_RELOAD;
  document.getElementById("reload-tech").value  = SHIP_TECH;
  document.getElementById("reload-cat").value   = SHIP_CAT;
  document.getElementById("reload-final").value = parseInt(SHIP_RELOAD) + parseInt(SHIP_TECH) + parseInt(SHIP_CAT);

  // deselect all
  document.getElementById("retrofit-select-true").removeAttribute("selected");
  document.getElementById("retrofit-select-false").removeAttribute("selected");
  document.getElementById("level-select-125").removeAttribute("selected");
  document.getElementById("level-select-120").removeAttribute("selected");
  document.getElementById("level-select-100").removeAttribute("selected");
  document.getElementById("oath-select-true").removeAttribute("selected");
  document.getElementById("oath-select-false").removeAttribute("selected");

  // select
  if (SHIP_KAI.availability)
  {
    document.getElementById("retrofit-select").removeAttribute("disabled");
    document.getElementById("retrofit-select-true").setAttribute("selected", "");
  }
  else
  {
    document.getElementById("retrofit-select").setAttribute("disabled", "");
    document.getElementById("retrofit-select-false").setAttribute("selected", "");
  };

  document.getElementById("level-select-125").setAttribute("selected", "");
  document.getElementById("oath-select-false").setAttribute("selected", "");
};
