import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Reload extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-reload">Rld</th>
        <UncontrolledTooltip placement="top" target="templateHead-reload" delay={0}><b>Reload</b></UncontrolledTooltip>
      </>
    );
  };
};
