import React, { Component } from "react";
import PropTypes from "prop-types";

import MeowfficerTalentList from "../../../assets/json/MeowfficerTalentList.json";

const propTypes = { talentID: PropTypes.string };

export default class MeowfficerTalentTemplate extends Component
{
  render()
  {
  	const TALENT_ID = this.props.talentID;

  	let talent = MeowfficerTalentList[0];
    for (let i = 0; i < MeowfficerTalentList.length; i++)
    {
      if (TALENT_ID === MeowfficerTalentList[i].id) talent = MeowfficerTalentList[i];
    };

    const TALENT_NAME = talent.name.format;
    const TALENT_DESC = talent.desc.format;
  	return (
  	  <tr>
  	  	<th>
  	  	  {talent.image.map((image, imgkey) =>
          {
            const IMG_KEYS = "TalentImageType" + imgkey + TALENT_ID;
            return (
              <img
                key={IMG_KEYS}
                className="img-fluid talentIcon"
                referrerPolicy="no-referrer"
                alt="..."
                src={image}
              />
            );
          })}
  	  	  <br />{TALENT_NAME}
  	  	</th>
  	  	<td>{TALENT_DESC}</td>
  	  </tr>
    );
  };
};

MeowfficerTalentTemplate.propTypes = propTypes;
