import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Damage extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-damage">Dmg</th>
        <UncontrolledTooltip placement="top" target="templateHead-damage" delay={0}><b>Damage</b></UncontrolledTooltip>
      </>
    );
  };
};
