import React, { Component } from "react";
import PropTypes from "prop-types";

// Component
import Vanguard      from "./TableBody/Vanguard.js";
import Main          from "./TableBody/Main.js";
import Torpedo       from "./TableBody/Torpedo.js";
import Submarine     from "./TableBody/Submarine.js";
import Plane         from "./TableBody/Plane.js";
import AntiAir       from "./TableBody/AntiAir.js";
import AntiSubmarine from "./TableBody/AntiSubmarine.js";
import Auxiliary     from "./TableBody/Auxiliary.js";

// Function
import typeGet from "./typeGet.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array,
  type: PropTypes.string
};

export default class TableBody extends Component
{
  render()
  {
    const NAVIGATE = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;
    const EQUIPMENT_TYPE = this.props.type;

    let equipment_template = typeGet(EQUIPMENT_TYPE);

    if (equipment_template === 1)
      return <Vanguard navigate={NAVIGATE} list={EQUIPMENT_LIST} type={EQUIPMENT_TYPE} />;

    if (equipment_template === 2)
      return <Main navigate={NAVIGATE} list={EQUIPMENT_LIST} type={EQUIPMENT_TYPE} />;

    if (equipment_template === 3)
      return <Torpedo navigate={NAVIGATE} list={EQUIPMENT_LIST} />;

    if (equipment_template === 4)
      return <Submarine navigate={NAVIGATE} list={EQUIPMENT_LIST} />;

    if (equipment_template === 5 || equipment_template === 6)
      return <Plane navigate={NAVIGATE} list={EQUIPMENT_LIST} type={EQUIPMENT_TYPE} />;

    if (equipment_template === 7)
      return <AntiAir navigate={NAVIGATE} list={EQUIPMENT_LIST} />;

    if (equipment_template === 8)
      return <AntiSubmarine navigate={NAVIGATE} list={EQUIPMENT_LIST} />;

    if (equipment_template === 9)
      return <Auxiliary navigate={NAVIGATE} list={EQUIPMENT_LIST} />;

    return null;
  };
};

TableBody.propTypes = propTypes;
