import ordnance from "./Plane/ordnance.js";
import guns from "./Plane/guns.js";

export default function modifier(equip, type, faction = "none")
{
  let ammo_type = "None";
  if (equip.ammoType) ammo_type = equip.ammoType.value;

  let shell_modifier = "";
  let light          = 100;
  let medium         = 100;
  let heavy          = 100;
  let shell_speed    = 0;
  let burn_chance    = 0;
  let splash_range   = 0;
  let spread         = 0;
  let armor_break    = 0;
  let knots          = 0;
  let units          = 0;

  function typeGet(bullet_type) { return ammo_type.includes(bullet_type) };

  if (type === "DD")
  {
    if (typeGet("Normal"))
    {
      shell_modifier = "Normal";
      light          = 100;
      medium         = 50;
      heavy          = 20;
      shell_speed    = 180;
    };

    if (typeGet("Normal*"))
    {
      shell_modifier = "Normal*";
      light          = 100;
      medium         = 55;
      heavy          = 25;
      shell_speed    = 180;
    };

    if (typeGet("Normal+"))
    {
      shell_modifier = "Normal+";
      light          = 100;
      medium         = 60;
      heavy          = 20;
      shell_speed    = 180;
    };

    if (typeGet("HE"))
    {
      shell_modifier = "HE";
      light          = 120;
      medium         = 60;
      heavy          = 60;
      shell_speed    = 150;
    };

    if (typeGet("AP"))
    {
      shell_modifier = "AP";
      light          = 90;
      medium         = 70;
      heavy          = 40;
      shell_speed    = 220;
    };

    if (typeGet("SAP"))
    {
      shell_modifier = "SAP";
      light          = 95;
      medium         = 95;
      heavy          = 25;
      shell_speed    = 220;
    };

    return ({ shell_modifier: shell_modifier, light: light, medium: medium, heavy: heavy, shell_speed: shell_speed });
  };

  if (type === "CL")
  {
    if (typeGet("Normal"))
    {
      shell_modifier = "Normal";
      light          = 100;
      medium         = 75;
      heavy          = 40;
      shell_speed    = 180;
    };

    if (typeGet("HE"))
    {
      shell_modifier = "HE";
      light          = 145;
      medium         = 105;
      heavy          = 70;
      shell_speed    = 150;
    };

    if (typeGet("HE+"))
    {
      shell_modifier = "HE+";
      light          = 145;
      medium         = 110;
      heavy          = 75;
      shell_speed    = 150;
    };

    if (typeGet("HEK"))
    {
      shell_modifier = "HEK";
      light          = 150;
      medium         = 105;
      heavy          = 70;
      shell_speed    = 150;
    };

    if (typeGet("AP"))
    {
      shell_modifier = "AP";
      light          = 100;
      medium         = 80;
      heavy          = 60;
      shell_speed    = 220;
    };

    if (typeGet("AP*"))
    {
      shell_modifier = "AP*";
      light          = 100;
      medium         = 100;
      heavy          = 60;
      shell_speed    = 220;
    };

    if (typeGet("AP+"))
    {
      shell_modifier = "AP+";
      light          = 110;
      medium         = 90;
      heavy          = 70;
      shell_speed    = 220;
    };

    if (typeGet("APK"))
    {
      shell_modifier = "APK";
      light          = 115;
      medium         = 85;
      heavy          = 65;
      shell_speed    = 220;
    };

    if (typeGet("APC28"))
    {
      shell_modifier = "APC28";
      light          = 100;
      medium         = 95;
      heavy          = 55;
      shell_speed    = 220;
    };


    return ({ shell_modifier: shell_modifier, light: light, medium: medium, heavy: heavy, shell_speed: shell_speed });
  };

  if (type === "CA")
  {
    if (typeGet("Normal"))
    {
      shell_modifier = "Normal";
      light          = 100;
      medium         = 90;
      heavy          = 50;
      shell_speed    = 180;
    };

    if (typeGet("NormalPR"))
    {
      shell_modifier = "NormalPR";
      light          = 115;
      medium         = 110;
      heavy          = 90;
      shell_speed    = 200;
    };

    if (typeGet("NormalDR"))
    {
      shell_modifier = "NormalDR";
      light          = 115;
      medium         = 115;
      heavy          = 95;
      shell_speed    = 200;
    };

    if (typeGet("HE"))
    {
      shell_modifier = "HE";
      light          = 135;
      medium         = 95;
      heavy          = 70;
      shell_speed    = 160;
      burn_chance    = 8;
    };

    if (typeGet("HE*"))
    {
      shell_modifier = "HE*";
      light          = 135;
      medium         = 95;
      heavy          = 70;
      shell_speed    = 180;
      burn_chance    = 9;
    };

    if (typeGet("AP"))
    {
      shell_modifier = "AP";
      light          = 75;
      medium         = 110;
      heavy          = 75;
      shell_speed    = 220;
    };

    if (typeGet("AP+"))
    {
      shell_modifier = "AP+";
      light          = 75;
      medium         = 110;
      heavy          = 85;
      shell_speed    = 220;
    };

    if (typeGet("SAP"))
    {
      shell_modifier = "SAP";
      light          = 65;
      medium         = 125;
      heavy          = 65;
      shell_speed    = 220;
    };


    return ({
      shell_modifier: shell_modifier,
      light: light,
      medium: medium,
      heavy: heavy,
      shell_speed: shell_speed,
      burn_chance: burn_chance
    });
  };

  if (type === "CB")
  {
    if (typeGet("HE"))
    {
      shell_modifier = "HE";
      light          = 135;
      medium         = 100;
      heavy          = 75;
      shell_speed    = 120;
      splash_range   = 8;
      spread         = 6;
      burn_chance    = 4;
    };

    if (typeGet("AP"))
    {
      shell_modifier = "AP";
      light          = 75;
      medium         = 110;
      heavy          = 75;
      shell_speed    = 140;
      splash_range   = 8;
    };

    if (typeGet("AP*"))
    {
      shell_modifier = "AP*";
      light          = 75;
      medium         = 115;
      heavy          = 75;
      shell_speed    = 140;
      splash_range   = 8;
      spread         = 6;
    };

    if (typeGet("APB"))
    {
      shell_modifier = "APB";
      light          = 70;
      medium         = 115;
      heavy          = 80;
      shell_speed    = 160;
      splash_range   = 10;
      spread         = 8;
    };

    return ({
      shell_modifier: shell_modifier,
      light: light,
      medium: medium,
      heavy: heavy,
      shell_speed: shell_speed,
      burn_chance: burn_chance,
      splash_range: splash_range,
      spread: spread
    });
  };

  if (type === "BB")
  {
    if (typeGet("Normal"))
    {
      shell_modifier = "Normal";
      light          = 70;
      medium         = 100;
      heavy          = 90;
      shell_speed    = 120;
      splash_range   = 15;
    };

    if (typeGet("HE"))
    {
      shell_modifier = "HE";
      light          = 140;
      medium         = 110;
      heavy          = 90;
      shell_speed    = 100;
      splash_range   = 15;
    };

    if (typeGet("AP"))
    {
      shell_modifier = "AP";
      light          = 45;
      medium         = 130;
      heavy          = 110;
      shell_speed    = 120;
      splash_range   = 15;
      armor_break    = 20;
    };

    if (typeGet("APMKD"))
    {
      shell_modifier = "APMKD";
      light          = 45;
      medium         = 130;
      heavy          = 115;
      shell_speed    = 120;
      splash_range   = 15;
      armor_break    = 20;
    };

    if (typeGet("AP*"))
    {
      shell_modifier = "AP*";
      light          = 40;
      medium         = 135;
      heavy          = 115;
      shell_speed    = 120;
      splash_range   = 15;
      armor_break    = 25;
    };

    if (typeGet("AP4"))
    {
      shell_modifier = "AP4";
      light          = 40;
      medium         = 140;
      heavy          = 115;
      shell_speed    = 140;
      splash_range   = 15;
      armor_break    = 20;
    };

    if (typeGet("AP^"))
    {
      shell_modifier = "AP^";
      light          = 40;
      medium         = 140;
      heavy          = 120;
      shell_speed    = 130;
      splash_range   = 15;
      armor_break    = 25;
    };

    if (typeGet("AP+"))
    {
      shell_modifier = "AP+";
      light          = 55;
      medium         = 145;
      heavy          = 125;
      shell_speed    = 120;
      splash_range   = 16;
      armor_break    = 20;
    };

    if (typeGet("SAP"))
    {
      shell_modifier = "SAP";
      light          = 100;
      medium         = 150;
      heavy          = 50;
      shell_speed    = 120;
      splash_range   = 15;
    };

    if (typeGet("Sanshikidan"))
    {
      shell_modifier = "Sanshikidan";
      light          = 140;
      medium         = 110;
      heavy          = 90;
      shell_speed    = 120;
      splash_range   = "-";
    };


    return ({
      shell_modifier: shell_modifier,
      light: light,
      medium: medium,
      heavy: heavy,
      shell_speed: shell_speed,
      splash_range: splash_range,
      armor_break: armor_break
    });
  };

  if (type === "TRP")
  {
    const CHARACTERISTIC = equip.characteristic.formatted;
    let torpedo_speed    = 0;

    light  = 80;
    medium = 100;
    heavy  = 130;

    if (CHARACTERISTIC === "Normal")
    {
      shell_modifier = "Normal";
      torpedo_speed  = 30;
    };

    if (CHARACTERISTIC === "Acoustic Homing")
    {
      shell_modifier = "Acoustic Homing";
      torpedo_speed  = 20;
    };

    if (CHARACTERISTIC === "Guided")
    {
      shell_modifier = "Guided";
      light          = 130;
      medium         = 110;
      heavy          = 80;
      torpedo_speed  = "80 (Vanguard)\n140 (Main Fleet)";
    };

    if (faction === "Royal Navy") torpedo_speed = 35;
    if (faction === "Sakura Empire") torpedo_speed = 40;

    return ({
      shell_modifier: shell_modifier,
      light: light,
      medium: medium,
      heavy: heavy,
      torpedo_speed: torpedo_speed
    });
  };

  if (type === "STRP")
  {
    if (typeGet("Normal") || typeGet("None"))
    {
      shell_modifier = "Normal";
      light          = 80;
      medium         = 100;
      heavy          = 130;
      knots          = 30;
      units          = 18;
    };

    if (typeGet("Acoustic"))
    {
      shell_modifier = "Acoustic";
      light          = 80;
      medium         = 100;
      heavy          = 130;
      knots          = 20;
      units          = 12;
    };

    if (typeGet("Oxygen"))
    {
      shell_modifier = "Oxygen";
      light          = 80;
      medium         = 100;
      heavy          = 130;
      knots          = 30;
      units          = 18;
    };

    if (typeGet("Normal*"))
    {
      shell_modifier = "Normal*";
      light          = 70;
      medium         = 100;
      heavy          = 120;
      knots          = 20;
      units          = 12;
    };

    if (typeGet("NormalS"))
    {
      shell_modifier = "NormalS";
      light          = 70;
      medium         = 100;
      heavy          = 120;
      knots          = 30;
      units          = 18;
    };

    return ({ shell_modifier: shell_modifier, light: light, medium: medium, heavy: heavy, knots: knots, units: units });
  };

  if (type === "F" || type === "DB")
  {
    let ordnance_list = [];
    let guns_list     = [];

    if (equip.ordnance)
    {
      equip.ordnance.stats.forEach((bomb) =>
      {
        const UNIT    = bomb.unit;
        const COUNT   = bomb.count;
        const FORMAT  = bomb.formatted;
        const EXTRACT = ordnance(UNIT);

        ordnance_list.push({ shell_modifier: UNIT, count: COUNT, format: FORMAT, ...EXTRACT });
      });
    };

    if (equip.aAGuns)
    {
      equip.aAGuns.stats.forEach((gun) =>
      {
        const UNIT    = gun.unit;
        const COUNT   = gun.count;
        const FORMAT  = gun.formatted;
        const EXTRACT = guns(FORMAT);

        guns_list.push({ shell_modifier: UNIT, count: COUNT, format: FORMAT, ...EXTRACT });
      });
    };

    return ({ ordnance: ordnance_list, guns: guns_list });
  };

  if (type === "TB")
  {
    const TORPEDO = equip.ordnance.stats[0];
    const UNIT    = TORPEDO.unit;
    const COUNT   = TORPEDO.count;
    const FORMAT  = TORPEDO.formatted;
    let guns_list = [];

    light  = 80;
    medium = 110;
    heavy  = 130;

    if (equip.aAGuns)
    {
      equip.aAGuns.stats.forEach((gun) =>
      {
        const extract = guns(gun.formattted);
        guns_list.push({ shell_modifier: gun.unit, count: gun.count, format: gun.formatted, ...extract });
      });
    };

    return ({
      torpedo: {
        shell_modifier: UNIT,
        count: COUNT,
        format: FORMAT,
        light: light,
        medium: medium,
        heavy: heavy
      },
      guns: guns_list
    });
  };

  return console.log("MODIFIER TYPE NOT FOUND");
};
