import React, { Component } from "react";
import PropTypes from "prop-types";

import ImgR from "../../../../components/ImgR.js";

const propTypes = {
  banner: PropTypes.any,
  id: PropTypes.string,
  name: PropTypes.string,
  toggle: PropTypes.func,
  kai: PropTypes.object
};

export default class ModalHeader extends Component
{
  render()
  {
    const BANNER = this.props.banner;
    const ID     = this.props.id;
    const NAME   = this.props.name;
    const TOGGLE = this.props.toggle;
    const KAI    = this.props.kai;

    let render_name = NAME;
    if (KAI.status) render_name = NAME + " (Retrofit)";
    return (
      <div className="modal-header dis-inlineblock text-center align-middle">
        <div className="dis-inlineblock"><ImgR id="slot-ship-banner" src={BANNER} /></div>
        <div
          className="font20px font-weight-700 text-uppercase text-black pt-2"
          id={ID}
        >{render_name}</div>
        <button
          type="button"
          className="close modal-close"
          data-dismiss="modal"
          aria-hidden="true"
          onClick={TOGGLE}
        >
          <i className="tim-icons icon-simple-remove font30px" />
        </button>
      </div>
    );
  };
};

ModalHeader.propTypes = propTypes;
