// ((((bomb1 * (am[0] / 100)) + (bomb2 * (am2[0] / 100))) * (coefficient / 100)) / fixedReload).toFixed(2); // fdive
// (((damage * ordnanceCount * 0.8) * (coefficient / 100)) / fixedReload).toFixed(2); // tbomb
// preload = (parseFloat(light) + ((fixedDamage * (80 / 100)) / 60)).toFixed(2); // tsurf
import modifier from "./modifier.js";
import ordnance from "./Plane/ordnance.js";

import pointRound  from "../../../../../../components/pointRound.js";

export default function dps(equip, armor_modifier, type, coef = "max", preload = false)
{
  if (type === "DD" || type === "CL" || type === "CA" || type === "CB" || type === "BB")
  {
    const DAMAGE            = parseFloat(equip.damage.max);
    const VOLLEY_MULTIPLIER = parseFloat(equip.volley.multiplier);
    const VOLLEY_COUNT      = parseFloat(equip.volley.count);
    const RATE_OF_FIRE      = parseFloat(equip.rateOfFire.max.replace("s", ""));
    const VOLLEY_TIME       = parseFloat(equip.volleyTime.value);
    const MODIFIER          = modifier(equip, type);

    let modifier_type = 100;
    if (armor_modifier === "Light") modifier_type = MODIFIER.light;
    if (armor_modifier === "Medium") modifier_type = MODIFIER.medium;
    if (armor_modifier === "Heavy") modifier_type = MODIFIER.heavy;

    let absolute_cooldown = 0;
    if (type === "DD") absolute_cooldown = 0.26;
    if (type === "CL") absolute_cooldown = 0.28;
    if (type === "CA") absolute_cooldown = 0.30;
    if (type === "CB") absolute_cooldown = 0.30;
    if (type === "BB") absolute_cooldown = 0;

    let coefficient = equip.coefficient;
    if (coefficient.value) coefficient = parseFloat(equip.coefficient.value);
    if (coefficient.max) coefficient = parseFloat(equip.coefficient.max.replace("%", ""));
    if (coef === "min") coefficient = parseFloat(equip.coefficient.min.replace("%", ""));

    const FORMULA_1       = (DAMAGE * (coefficient / 100) * (VOLLEY_MULTIPLIER * VOLLEY_COUNT) * (modifier_type / 100));
    const ABSOLUTE_RELOAD = (RATE_OF_FIRE + VOLLEY_TIME + absolute_cooldown);
    const FORMULA         = FORMULA_1 / ABSOLUTE_RELOAD;

    return pointRound(FORMULA).toString();
  };

  if (type === "TRP" || type === "STRP")
  {
    const DAMAGE       = parseFloat(equip.damage.max);
    const COUNT        = parseFloat(equip.noOfTorpedoes.value);
    const RATE_OF_FIRE = parseFloat(equip.rateOfFire.max.replace("s", ""));
    const MODIFIER     = modifier(equip, type);

    let modifier_type = 100;
    if (armor_modifier === "Light") modifier_type = MODIFIER.light;
    if (armor_modifier === "Medium") modifier_type = MODIFIER.medium;
    if (armor_modifier === "Heavy") modifier_type = MODIFIER.heavy;

    const FORMULA         = ((DAMAGE * COUNT * (modifier_type / 100)) / RATE_OF_FIRE);
    const PRELOAD_TORPEDO = (FORMULA + ((DAMAGE * COUNT * (modifier_type / 100)) / 60));

    if (preload) return pointRound(PRELOAD_TORPEDO).toString();
    if (!preload) return pointRound(FORMULA).toString();
  };

  if (type === "F" || type === "DB" || type ===  "SP")
  {
    // sum(ordnance dmg x armor mod ordnance) / rof x 2.2 + absolute cooldown
    if (!equip.ordnance) return "0.00";

    const ORDNANCE          = equip.ordnance.stats;
    const RATE_OF_FIRE      = parseFloat(equip.rateOfFire.max.replace(/[^0-9$.]/g, ""));
    const ABSOLUTE_COOLDOWN = 0.1;

    let ordnance_damage = 0;
    ORDNANCE.forEach((idx) =>
    {
      const ORDNANCE_TYPE  = idx.unit;
      const ORDNANCE_COUNT = parseInt(idx.count);
      const ORDNANCE_STATS = ordnance(ORDNANCE_TYPE);

      const ORDNANCE_DAMAGE   = ORDNANCE_STATS.damage;
      const ORDNANCE_MODIFIER = ORDNANCE_STATS[armor_modifier.toLowerCase()] / 100;
      ordnance_damage += (ORDNANCE_DAMAGE * ORDNANCE_COUNT) * ORDNANCE_MODIFIER;
    });

    return pointRound(ordnance_damage / (RATE_OF_FIRE * 2.2 + ABSOLUTE_COOLDOWN)).toString();
  };

  if (type === "TB")
  {
    const TORPEDO_COUNT     = equip.ordnance.stats[0].count;
    const TORPEDO_DAMAGE    = parseInt(equip.damage.max);
    const RATE_OF_FIRE      = parseFloat(equip.rateOfFire.max.replace(/[^0-9$.]/g, ""));
    const ABSOLUTE_COOLDOWN = 0.1;

    let torpedo_modifier = 1.0;
    if (armor_modifier === "Light") torpedo_modifier = 0.80;
    if (armor_modifier === "Medium") torpedo_modifier = 1.10;
    if (armor_modifier === "Heavy") torpedo_modifier = 1.30;

    const ORDNANCE_DAMAGE = (TORPEDO_COUNT * TORPEDO_DAMAGE) * torpedo_modifier;

    return pointRound(ORDNANCE_DAMAGE / (RATE_OF_FIRE * 2.2 + ABSOLUTE_COOLDOWN)).toString();
  };

  console.log("DPS TYPE NOT FOUND!");

  return "0.00";
};
