import React, { Component } from "react";
import PropTypes from "prop-types";

// Components
import ImgR from "../../../../../components/ImgR.js";

// Functions
import factionClass from "../../../../../components/factionClass.js";
import raw from "./Formula/raw.js";
import dps from "./Formula/dps.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array,
  type: PropTypes.string
};

export default class Main extends Component
{
  render()
  {
    const NAVIGATE = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;
    const EQUIPMENT_TYPE = this.props.type;

    let gear_type = "NONE";
    if (EQUIPMENT_TYPE === "large_cruiser_guns") gear_type = "CB";
    if (EQUIPMENT_TYPE === "battleship_guns") gear_type = "BB";

    return (
      <tbody>
        {EQUIPMENT_LIST.map((equip, key) =>
        {
          const ICON = equip.image;
          const NAME = equip.names.en;

          const FACTION       = equip.nationality;
          const FACTION_CLASS = factionClass(FACTION);

          const EQUIPMENT_RARITY = equip.highest_rarity;
          const EQUIPMENT_LINK   = encodeURIComponent(NAME.replace(/\s+/g, "_"));
          const STATS = equip.stats;

          let firepower = 0;
          let antiair   = 0;
          let spread    = 0;
          let range     = STATS.range.formatted;

          // Firepower
          if (STATS.firepower) firepower = STATS.firepower.formatted;

          // Anti-Air
          if (STATS.antiair) antiair = STATS.antiair.formatted;

          // Spread
          if (STATS.spread) spread = STATS.spread.formatted;
          if (NAME === "Triple 152mm (Model 1934)") spread = "18°";

          // Range
          if (STATS.range)
          {
            if (STATS.range.firing && gear_type === "BB") range = STATS.range.firing;
            if (STATS.range.shell && gear_type === "CB") range = STATS.range.shell;
          };

          const DAMAGE         = STATS.damage.max;
          const RATE_OF_FIRE   = STATS.rateOfFire.max;
          const ANGLE          = STATS.angle.formatted;
          const VOLLEY         = STATS.volley.multiplier + "×" + STATS.volley.count;
          const VOLLEY_TIME    = STATS.volleyTime.formatted;
          const COEFFICIENT    = STATS.coefficient.max;
          const CHARACTERISTIC = STATS.characteristic.formatted;

          const AMMO_TYPE   = STATS.ammoType.formatted;
          const AMMO_STRING = AMMO_TYPE.substring(0, AMMO_TYPE.indexOf(" "));
          const AMMO_NUMBER = AMMO_TYPE.substring(AMMO_TYPE.indexOf(" ") + 1);
          const AMMO_FORMAT = (<><p>{AMMO_STRING}</p><p>{AMMO_NUMBER}</p></>);

          const RAW    = raw(STATS, gear_type);
          const LIGHT  = dps(STATS, "Light", gear_type);
          const MEDIUM = dps(STATS, "Medium", gear_type);
          const HEAVY  = dps(STATS, "Heavy", gear_type);

          return (
            <tr key={key} onClick={() => NAVIGATE(EQUIPMENT_LINK)}>
              <td>
                <div className={"m-auto equipment-rarity-" + EQUIPMENT_RARITY}><ImgR src={ICON} className="p-10" /></div>
              </td>
              <td>{NAME}</td>
              <td>{firepower}</td>
              <td>{antiair}</td>
              <td>{VOLLEY}</td>
              <td>{DAMAGE}</td>
              <td>{COEFFICIENT}</td>
              <td>{VOLLEY_TIME}</td>
              <td>{RATE_OF_FIRE}</td>
              <td className="dps-r">{RAW}</td>
              <td className="dps-l">{LIGHT}</td>
              <td className="dps-m">{MEDIUM}</td>
              <td className="dps-h">{HEAVY}</td>
              <td>{range}</td>
              <td>{spread}</td>
              <td>{ANGLE}</td>
              <td>{CHARACTERISTIC}</td>
              <td>{AMMO_FORMAT}</td>
              <td className={FACTION_CLASS + " faction-background"}>{FACTION}</td>
            </tr>
          );
        })}
      </tbody>
    );
  };
};

Main.propTypes = propTypes;
