import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class AntiAirDPS extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-antiairdps">AA DPS</th>
        <UncontrolledTooltip placement="top" target="templateHead-antiairdps" delay={0}><b>Anti-Air DPS</b></UncontrolledTooltip>
      </>
    );
  };
};
