import React, { Component } from "react";
import PropTypes from "prop-types";
import { Col, NavLink, Card, CardText } from "reactstrap";

const propTypes = {
  tag: PropTypes.object,
  to: PropTypes.string
};

export default class EquipmentCard extends Component
{
  render()
  {
    const Link = this.props.tag;
    const text_link = this.props.to;
    const card_link = "/equipment/" + text_link.toLowerCase().replace(/ /g, "_");
    return (
      <Col xs="12" sm="6">
        <div>
          <NavLink tag={Link} to={card_link}>
            <Card body outline color="primary">
              <CardText>{text_link}</CardText>
            </Card>
          </NavLink>
        </div>
      </Col>
    );
  };
};

EquipmentCard.propTypes = propTypes;

