import React, { Component } from "react";
import PropTypes from "prop-types";

import MeowfficerData from "../../assets/json/MeowfficerData.json";

const propTypes = {
  codex: PropTypes.number,
  openModal: PropTypes.func
};

export default class MeowfficerList extends Component
{
  render()
  {
    let idx = this.props.codex;
    if (idx === null || idx === undefined) idx = 0;

    const MEOWFFICER = MeowfficerData[idx];
    const CAT_COLOR  = "cat" + MEOWFFICER.color;
    const CAT_NAME   = MEOWFFICER.name;
    const CAT_ICON   = MEOWFFICER.icon;

    const openModal = () =>
    {
      this.props.openModal({
        name: CAT_NAME,
        codex: idx
      });
    };
    return (
      <div className="cardCS" onClick={openModal}>
        <div className="meowfficerBorder">
          <img src={CAT_ICON} className={"meowfficerIcon img-fluid " + CAT_COLOR} referrerPolicy="no-referrer" alt="..." />
        </div>
        <div className="align-middle meowfficerName">{CAT_NAME}</div>
      </div>
    );
  };
};

MeowfficerList.propTypes = propTypes;
