import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Alert } from "reactstrap";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

import UnknownBanner from "../../assets/AL/UnknownBanner.png";

const propTypes = {
  azurAPI: PropTypes.any,
  shipBanner: PropTypes.any
};

export default class ShipList extends Component
{
  render()
  {
    const AZURAPI     = this.props.azurAPI;
    const SHIP_BANNER = this.props.shipBanner;
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <Alert color="warning" className="text-center align-middle">
            This page is under Development!
          </Alert>
          <div className="main text-center align-middle shipheader">
            <div className="content">
              <h1>Ship List</h1>
              <div className="containerCS">
                {AZURAPI.map((ship) =>
                {
                  const RARITY  = ship.rarity;
                  const SHIP_ID = ship.id;
                  let ship_name = ship.names.en;
                  if (SHIP_ID === "30001") {ship_name = "Hiryuu META"};
                  if (SHIP_ID === "30002") {ship_name = "Ark Royal META"};

                  const SHIP_LINK   = encodeURIComponent(ship_name.replace(/\s+/g, "_"));
                  const BANNER_DATA = SHIP_BANNER.find(banner => banner.id === SHIP_ID);

                  let banner_rarity  = "rarity-0";
                  let banner_status  = true;
                  let default_banner = UnknownBanner;

                  if (BANNER_DATA) default_banner = BANNER_DATA.skins[0].banner; banner_status = false;
                  if (banner_status) {console.log(ship_name); console.log(ship)};

                  if (RARITY === "Normal")
                    banner_rarity = "rarity-1";

                  if (RARITY === "Rare")
                    banner_rarity = "rarity-2";

                  if (RARITY === "Elite")
                    banner_rarity = "rarity-3";

                  if (RARITY === "Super Rare" || RARITY === "Priority")
                    banner_rarity = "rarity-4";

                  if (RARITY === "Ultra Rare" || RARITY === "Decisive")
                    banner_rarity = "rarity-5";

                  return (
                    <div className="cardCS shipCard" key={ship._gid}>
                      <Link to={`/ship/${SHIP_LINK}`} className="shipName linkCS">
                        <div className={banner_rarity + " bannerContainer"}>
                          <img src={default_banner} referrerPolicy="no-referrer" alt="..." className="img-fluid shipBanner" />
                        </div>
                        <br />
                        {ship_name}
                      </Link >
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

ShipList.propTypes = propTypes;
