import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Range extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-range">Rng</th>
        <UncontrolledTooltip placement="top" target="templateHead-range" delay={0}><b>Range</b></UncontrolledTooltip>
      </>
    );
  };
};
