import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = { obtainedFrom: PropTypes.string };

export default class Obtained extends Component
{
  render()
  {
    const obtained_from = this.props.obtainedFrom;
    if (obtained_from !== "")
    {
      return (
        <Table>
          <thead>
            <tr>
              <th>Obtained From</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{obtained_from}</td>
            </tr>
          </tbody>
        </Table>
      );
    };
    return null;
  };
};

Obtained.propTypes = propTypes;
