import React, { Component } from "react";
import PropTypes from "prop-types";

const propTypes = {
  src: PropTypes.string,
  className: PropTypes.string,
  id: PropTypes.string,
  none: PropTypes.bool
};

export default class Fits extends Component
{
  render()
  {
    const source = this.props.src;
    const gclass = this.props.className;
    const gid    = this.props.id;

    if (this.props.none) return <th className={gclass} id={gid} />;

    return <th className={gclass} id={gid} ><img src={source} referrerPolicy="no-referrer" alt="..." className="img-fluid" /></th>;
  };
};

Fits.propTypes = propTypes;

