import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import Torpedo_img from "../../../../../assets/AL/Torpedo.png";
import ImgR from "../../../../../components/ImgR.js";

export default class Torpedo extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-torpedo"><ImgR src={Torpedo_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-torpedo" delay={0}><b>Torpedo</b></UncontrolledTooltip>
      </>
    );
  };
};
