import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Row, Alert } from "reactstrap";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

import EquipmentCard from "./EquipmentList/EquipmentCard.js";

export default class EquipmentList extends Component
{
  render()
  {
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="equipmentPageHeader">
            <div className="content-center">
              <Alert color="warning" className="text-center align-middle">
                This page is under Development!
              </Alert>
              <Row className="row-grid justify-content-between align-items-center">
                <EquipmentCard tag={Link} to="Destroyer Guns" />
                <EquipmentCard tag={Link} to="Light Cruiser Guns" />
                <EquipmentCard tag={Link} to="Heavy Cruiser Guns" />
                <EquipmentCard tag={Link} to="Large Cruiser Guns" />
                <EquipmentCard tag={Link} to="Battleship Guns" />
                <EquipmentCard tag={Link} to="Ship Torpedoes" />
                <EquipmentCard tag={Link} to="Submarine Torpedoes" />
                <EquipmentCard tag={Link} to="Fighter Planes" />
                <EquipmentCard tag={Link} to="Dive Bomber Planes" />
                <EquipmentCard tag={Link} to="Torpedo Bomber Planes" />
                <EquipmentCard tag={Link} to="Seaplanes" />
                <EquipmentCard tag={Link} to="Anti-Air Guns" />
                <EquipmentCard tag={Link} to="Anti-Submarine Equipment" />
                <EquipmentCard tag={Link} to="Auxiliary Equipment" />
              </Row>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};
