import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Ordnance extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-ordnance">Ordnance</th>
        <UncontrolledTooltip placement="top" target="templateHead-ordnance" delay={0}><b>Ordnance</b></UncontrolledTooltip>
      </>
    );
  };
};
