import React, { Component } from "react";
import PropTypes from "prop-types";
import { Navigate } from "react-router-dom";

import ProgressLoad from "../../components/ProgressLoad.js";
import ShipProfile  from "./ShipProfile.js";

const propTypes = { paramsId: PropTypes.string };

export default class ShipFetch extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      azurAPI: [],
      shipBanner: []
    };
  };

  componentDidMount()
  {
    this.mounted = true;
    Promise.all([
      fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
        .then(res => res.json()),
      fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
        .then(res => res.json())
    ]).then(
      ([azurAPI, shipBanner]) =>
      {
        if (this.mounted)
        {
          this.setState({
            isLoaded: true,
            azurAPI: azurAPI,
            shipBanner: shipBanner
          });
        };
      },
      (error) =>
      {
        this.setState({
          isLoaded: true,
          error
        });
      }
    );
  };

  componentWillUnmount() { this.mounted = false };

  render()
  {
    const { error, isLoaded, azurAPI, shipBanner } = this.state;
    if (error)
      return <div>Error: {error.message}</div>;
    else if (!isLoaded)
      return <ProgressLoad text="Loading..." />;

    const SHIP_NAME   = this.props.paramsId;
    const SHIP_DATA   = azurAPI.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === SHIP_NAME.toLowerCase());
    const BANNER_DATA = shipBanner.find(banner => banner.names === SHIP_NAME);

    if (SHIP_DATA === undefined)
      return <Navigate to="/ship" />;

    return <ShipProfile shipData={SHIP_DATA} bannerData={BANNER_DATA} />;
  };
};

ShipFetch.propTypes = propTypes;
