import React, { Component } from "react";
import PropTypes from "prop-types";
import { Alert } from "reactstrap";

import Navigator from "../../../components/Navigator/Navigator.js";

const propTypes = {
  names: PropTypes.object,
  image: PropTypes.string
};

export default class EquipmentHeader extends Component
{
  render()
  {
    const equipment_names = this.props.names;
    const equipment_image = this.props.image;
    return (
      <>
        <Navigator equipment path={equipment_names.en} />
        <Alert color="warning">
          This page is under Development!
        </Alert>
        <div>
          <div className={"dis-inlineblock"}>
            <img src={equipment_image} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
          </div>
          <h1>{equipment_names.en}</h1>
        </div>
      </>
    );
  };
};

EquipmentHeader.propTypes = propTypes;
