import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import AntiAir_img from "../../../../../assets/AL/AntiAir.png";
import ImgR from "../../../../../components/ImgR.js";

export default class AntiAir extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-antiair"><ImgR src={AntiAir_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-antiair" delay={0}><b>Anti-Air</b></UncontrolledTooltip>
      </>
    );
  };
};
