import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Notes extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-notes">Notes</th>
        <UncontrolledTooltip placement="top" target="templateHead-notes" delay={0}><b>Notes</b></UncontrolledTooltip>
      </>
    );
  };
};
