export default function typeGet(type)
{
  let equipment_template = 0;
  if (type === "destroyer_guns" || type === "light_cruiser_guns" || type === "heavy_cruiser_guns")
    equipment_template = 1;

  if (type === "large_cruiser_guns" || type === "battleship_guns")
    equipment_template = 2;

  if (type === "ship_torpedoes")
    equipment_template = 3;

  if (type === "submarine_torpedoes")
    equipment_template = 4;

  if (type === "fighter_planes" || type === "dive_bomber_planes" || type === "seaplanes")
    equipment_template = 5;

  if (type === "torpedo_bomber_planes")
    equipment_template = 6;

  if (type === "anti-air_guns")
    equipment_template = 7;

  if (type === "anti-submarine_equipment")
    equipment_template = 8;

  if (type === "auxiliary_equipment")
    equipment_template = 9;

  return equipment_template;
};
