import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Reloads extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-reloads">Rld(s)</th>
        <UncontrolledTooltip placement="top" target="templateHead-reloads" delay={0}><b>Reload(s)</b></UncontrolledTooltip>
      </>
    );
  };
};
