import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Icon extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-icon">Icon</th>
        <UncontrolledTooltip placement="top" target="templateHead-icon" delay={0}><b>Equipment Icon</b></UncontrolledTooltip>
      </>
    );
  };
};
