import React, { Component } from "react";
import PropTypes from "prop-types";
import { Alert, Button, Badge } from "reactstrap";

import Navigator     from "../../../components/Navigator/Navigator.js";
import UnknownBanner from "../../../assets/AL/UnknownBanner.png";

const propTypes = {
  bannerData: PropTypes.any,
  names: PropTypes.object,
  rarity: PropTypes.string,
  id: PropTypes.string
};

export default class ShipHeader extends Component
{
  render()
  {
    const BANNER_DATA = this.props.bannerData;
    const NAMES       = this.props.names;
    const RARITY      = this.props.rarity;
    const SHIP_ID     = this.props.id;

    let ship_name = NAMES.en;
    let code_name = NAMES.code;

    if (SHIP_ID === "30001") ship_name = "Hiryuu META";
    if (SHIP_ID === "30002") ship_name = "Ark Royal META";

    let banner_rarity  = "rarity-0";
    let banner_status  = true;
    let default_banner = UnknownBanner;

    if (BANNER_DATA) { default_banner = BANNER_DATA.skins[0].banner; banner_status = false };
    if (banner_status) console.log(ship_name);

    if (RARITY === "Normal")
      banner_rarity = "rarity-1";

    if (RARITY === "Rare")
      banner_rarity = "rarity-2";

    if (RARITY === "Elite")
      banner_rarity = "rarity-3";

    if (RARITY === "Super Rare" || RARITY === "Priority")
      banner_rarity = "rarity-4";

    if (RARITY === "Ultra Rare" || RARITY === "Decisive")
      banner_rarity = "rarity-5";

    return (
      <>
        <Navigator ship path={ship_name} />
        <Alert color="warning">
          This page is under Development!
        </Alert>
        <div>
          <div className={banner_rarity + " dis-inlineblock"}>
            <img src={default_banner} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
          </div>
          <h1>{ship_name}</h1>
          <h6>{code_name}</h6>
          <Button color="primary" size="sm" block disabled>Skin List<Badge color="info">Comming Soon!</Badge></Button>
        </div>
      </>
    );
  };
};

ShipHeader.propTypes = propTypes;
