import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Light extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-light" className="dps-l">L</th>
        <UncontrolledTooltip placement="top" target="templateHead-light" delay={0}><b>DPS vs Light Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
