export default function rarityGet(ship_rarity, type = 1)
{
  const RARITY = ship_rarity;
  if (type === 1)
  {
    if (RARITY === 1) return "Normal";
    if (RARITY === 2) return "Rare";
    if (RARITY === 3) return "Elite";
    if (RARITY === 4) return "Super Rare";
    if (RARITY === 5) return "Ultra Rare";
  };

  if (type === 2)
  {
    if (RARITY === "Normal") return 1;
    if (RARITY === "Rare") return 2;
    if (RARITY === "Elite") return 3;
    if (RARITY === "Super Rare") return 4;
    if (RARITY === "Ultra Rare") return 5;
    if (RARITY === "Priority") return 4;
    if (RARITY === "Decisive") return 5;
  };

  if (type === 3)
  {
    if (RARITY === "Normal") return "Rare";
    if (RARITY === "Rare") return "Elite";
    if (RARITY === "Elite") return "Super Rare";
    if (RARITY === "Super Rare") return "Ultra Rare";
  };

  if (type === 4)
  {
    if (RARITY === "Rare") return "Normal";
    if (RARITY === "Elite") return "Rare";
    if (RARITY === "Super Rare") return "Elite";
    if (RARITY === "Ultra Rare") return "Super Rare";
  };

  if (type === 5) return ship_rarity + 1;
  if (type === 6) return ship_rarity - 1;

  return null;
};
