import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import Firepower_img from "../../../../../assets/AL/Firepower.png";
import ImgR from "../../../../../components/ImgR.js";

export default class Firepower extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-firepower"><ImgR src={Firepower_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-firepower" delay={0}><b>Firepower</b></UncontrolledTooltip>
      </>
    );
  };
};
