import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
} from "chart.js";
import { Radar } from "react-chartjs-2";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

const propTypes = {
  name: PropTypes.string,
  data: PropTypes.array
};

export default class ShipChart extends Component
{
  render()
  {
    const SHIP_NAME    = this.props.name;
    const HEXAGON_DATA = this.props.data;
    const DATA = {
      labels: ["FP", "HP", "AA", "EVA", "AVI", "TRP"],
      datasets: [
        {
          label: SHIP_NAME,
          data: HEXAGON_DATA,
          backgroundColor: "rgba(255, 99, 132, 0.2)",
          borderColor: "rgba(255, 99, 132, 1)",
          borderWidth: 1
        }
      ]
    };

    const OPTIONS = {
      plugins: { legend: { display: false } },
      scales: {
        r: {
          suggestedMin: 0,
          suggestedMax: 5,
          ticks: { stepSize: 1, display: false },
          pointLabels: {
            color: "rgba(255, 255, 255, 0.7)",
            font: {
              family: "'Poppins', sans-serif",
              size: 12,
              weight: 700,
              lineHeight: 1.5
            }
          },
          angleLines: { color: "red" },
          grid: { color: ["teal", "green", "orange", "yellow", "red"] }
        }
      }
    };

    return <div className="shipChart m-auto"><Radar data={DATA} options={OPTIONS} /></div>;
  };
};

ShipChart.propTypes = propTypes;
