import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class PreloadLight extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-preloadlight" className="dps-pl">PL</th>
        <UncontrolledTooltip placement="top" target="templateHead-preloadlight" delay={0}><b>Preload DPS vs Light Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
