import guns from "./Plane/guns.js";

export default function antiAir(equip, type, id)
{
  if (!equip.aAGuns) return { dps: "0.00", burst: "0.00" };

  const RATE_OF_FIRE      = parseFloat(equip.rateOfFire.max.replace(/[^0-9$.]/g, ""));
  const SPEED             = parseInt(equip.speed.formatted);
  const ABSOLUTE_COOLDOWN = 0.1;
  const GUN_LIST          = equip.aAGuns.stats;

  const DPS_INTERVAL      = 1 / (RATE_OF_FIRE * 2.2 + ABSOLUTE_COOLDOWN);
  const DPS_INTERCEPTOR   = 1 / (RATE_OF_FIRE + ABSOLUTE_COOLDOWN);

  let non_interceptor  = ["Messerschmitt Bf 109G (Carrier-based Prototype)"];
  let interceptor_list = ["Kawanishi N1K1 Kyoufuu", "Nakajima A6M2-N", "Westland Wyvern"];

  let interceptor = false;
  let gun_damage  = 0;
  let gun_burst   = 0;

  if (type === "F") interceptor = true;
  if (non_interceptor.find(idx => idx === id)) interceptor = false;
  if (interceptor_list.find(idx => idx === id)) interceptor = true;

  GUN_LIST.forEach((gun) =>
  {
    const GUN_DATA = guns(gun.formatted);
    const ATTACK_RUN_DURATION = (125 / (0.6 * SPEED)) - 1.5;
    const SHOT_PER_ATTACK_RUN = (ATTACK_RUN_DURATION * (150.359569 / GUN_DATA.firerate)) + 0.5;

    gun_damage += (GUN_DATA.damage * SHOT_PER_ATTACK_RUN);
    gun_burst += GUN_DATA.damage;
  });

  let launch_dps = DPS_INTERVAL;
  if (interceptor) launch_dps = DPS_INTERVAL + DPS_INTERCEPTOR;

  const AA_DPS   = (gun_damage * launch_dps).toFixed(2).toString();
  const AA_BURST = (gun_burst * launch_dps).toFixed(2).toString();

  return { dps: AA_DPS, burst: AA_BURST };
};
