import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Guns extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-guns">Guns</th>
        <UncontrolledTooltip placement="top" target="templateHead-guns" delay={0}><b>Guns</b></UncontrolledTooltip>
      </>
    );
  };
};
