import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Angle extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-angle">Angle</th>
        <UncontrolledTooltip placement="top" target="templateHead-angle" delay={0}><b>Angle</b></UncontrolledTooltip>
      </>
    );
  };
};
