import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Nation extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-nation">Nation</th>
        <UncontrolledTooltip placement="top" target="templateHead-nation" delay={0}><b>Equipment Nation</b></UncontrolledTooltip>
      </>
    );
  };
};
