import React, { Component } from "react";
import PropTypes from "prop-types";

import EquipmentFetch   from "./EquipmentFetch.js";
import EquipmentProfile from "./EquipmentProfile.js";

const propTypes = {
  equipmentData: PropTypes.object,
  paramsId: PropTypes.string
};

export default class EquipmentRoute extends Component
{
  render()
  {
  	const EQUIPMENT_DATA = this.props.equipmentData;
    const PARAMS_ID      = this.props.paramsId;
  	if (EQUIPMENT_DATA === undefined)
  	  return <EquipmentFetch paramsId={PARAMS_ID} />;

    return <EquipmentProfile equipmentData={EQUIPMENT_DATA} />;
  };
};

EquipmentRoute.propTypes = propTypes;

