import React, { Component } from "react";
import PropTypes from "prop-types";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

// Build Components
import ShipHeader   from "./ShipProfile/ShipHeader.js";
import ShipChart    from "./ShipProfile/ShipChart.js";
import ShipMetadata from "./ShipProfile/ShipMetadata.js";
import ShipStats    from "./ShipProfile/ShipStats.js";
import ShipTech     from "./ShipProfile/ShipTech.js";
import ShipSlots    from "./ShipProfile/ShipSlots.js";
import ShipBreaks   from "./ShipProfile/ShipBreaks.js";
import ShipSkills   from "./ShipProfile/ShipSkills.js";

const propTypes = {
  shipData: PropTypes.any,
  bannerData: PropTypes.any
};

export default class ShipProfile extends Component
{
  render()
  {
    // Main
    const SHIP_DATA   = this.props.shipData;
    const BANNER_DATA = this.props.bannerData;

    // Metadata
    const ID    = SHIP_DATA.id;
    const NAMES = SHIP_DATA.names;

    const CLASS_TYPE  = SHIP_DATA.class;
    const NATIONALITY = SHIP_DATA.nationality;
    const HULL_TYPE   = SHIP_DATA.hullType;
    const RARITY      = SHIP_DATA.rarity;

    const CONSTRUCTION_TIME = SHIP_DATA.construction.constructionTime;
    const OBTAINED_FROM     = SHIP_DATA.obtainedFrom.obtainedFrom;

    // Metadata with Child
    const EXIST         = SHIP_DATA.exists;
    const AVAILABLE_IN  = SHIP_DATA.construction.availableIn;
    const ENHANCE_VALUE = SHIP_DATA.enhanceValue;
    const SCRAP_VALUE   = SHIP_DATA.scrapValue;

    // Hexagon
    const HEXAGON_DATA = SHIP_DATA.hexagon;

    // Fleet Technology
    const FLEET_TECH = SHIP_DATA.fleetTech;

    // Slots
    const SLOTS = SHIP_DATA.slots;

    // Skin
    // const skinName       = shipData.skins[0].name;
    // const skinImage      = shipData.skins[0].image;
    // const skinBackground = shipData.skins[0].background;
    // const skinChibi      = shipData.skins[0].chibi;
    // // skinInfo child => {live2d, obtainedFrom} (type: bool, string)
    // const skinInfo       = shipData.skins[0].info;


    // Stats
    const BASE_STATS = SHIP_DATA.stats.baseStats;
    const LEVEL_100  = SHIP_DATA.stats.level100;
    const LEVEL_120  = SHIP_DATA.stats.level120;
    const LEVEL_125  = SHIP_DATA.stats.level125;


    // LimitBreaks & devLevels
    let limit_breaks = true;
    if (SHIP_DATA.limitBreaks === undefined) limit_breaks = false;

    let dev_breaks = null;
    if (limit_breaks) dev_breaks = SHIP_DATA.limitBreaks;
    else dev_breaks = SHIP_DATA.devLevels;

    // skills
    const SKILLS = SHIP_DATA.skills;


    // Retrofit
    let retrofit = false;
    if (SHIP_DATA.retrofit === true) retrofit = true;

    let retrofit_id = null;
    let retrofit_hull_type = null;
    let level_100_retrofit = null;
    let level_120_retrofit = null;
    let level_125_retrofit = null;

    if (retrofit)
    {
      retrofit_id = SHIP_DATA.retrofitId;
      retrofit_hull_type = SHIP_DATA.retrofitHullType;
      level_100_retrofit = SHIP_DATA.stats.level100Retrofit;
      level_120_retrofit = SHIP_DATA.stats.level120Retrofit;
      level_125_retrofit = SHIP_DATA.stats.level125Retrofit;
    };

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle shipheader">
            <div className="content shipContent">
              <ShipHeader
                bannerData={BANNER_DATA}
                names={NAMES}
                rarity={RARITY}
                id={ID}
              />
              <ShipMetadata
                retrofit={retrofit}
                retrofitId={retrofit_id}
                retrofitHullType={retrofit_hull_type}
                id={ID}
                classType={CLASS_TYPE}
                nationality={NATIONALITY}
                hullType={HULL_TYPE}
                rarity={RARITY}
                constructionTime={CONSTRUCTION_TIME}
                obtainedFrom={OBTAINED_FROM}
                exist={EXIST}
                availableIn={AVAILABLE_IN}
                enhanceValue={ENHANCE_VALUE}
                scrapValue={SCRAP_VALUE}
              />
              <ShipChart name={NAMES.en} data={HEXAGON_DATA} />
              <ShipStats
                retrofit={retrofit}
                baseStats={BASE_STATS}
                level100={LEVEL_100}
                level120={LEVEL_120}
                level125={LEVEL_125}
                level100Retrofit={level_100_retrofit}
                level120Retrofit={level_120_retrofit}
                level125Retrofit={level_125_retrofit}
              />
              <ShipTech
                fleetTech={FLEET_TECH}
              />
              <ShipSlots
                slots={SLOTS}
              />
              <ShipBreaks
                limitBreaks={limit_breaks}
                devBreaks={dev_breaks}
              />
              <ShipSkills
                skills={SKILLS}
              />
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

ShipProfile.propTypes = propTypes;
