import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table, Modal, ModalBody } from "reactstrap";

const propTypes = {
  skillModalOpen: PropTypes.bool,
  skillModalClose: PropTypes.func,
  skill: PropTypes.object
};

export default class MeowfficerSkill extends Component
{
  render()
  {
  	const SKILL_MODAL_OPEN  = this.props.skillModalOpen;
	  const SKILL_MODAL_CLOSE = this.props.skillModalClose;
	  const SKILL_DATA        = this.props.skill;

	  const SKILL_IMAGE = SKILL_DATA.image;
	  const SKILL_NAME  = SKILL_DATA.name;

	  const SKILL_LEVEL_1 = SKILL_DATA.general.level1;
	  const SKILL_LEVEL_2 = SKILL_DATA.general.level2;
	  const SKILL_LEVEL_3 = SKILL_DATA.general.level3;

	  const SKILL_OPSI_1 = SKILL_DATA.opsi.level1;
	  const SKILL_OPSI_2 = SKILL_DATA.opsi.level2;
	  const SKILL_OPSI_3 = SKILL_DATA.opsi.level3;

    return (
      <Modal isOpen={SKILL_MODAL_OPEN} toggle={SKILL_MODAL_CLOSE} size="xl" className="MeowfficerSkillModal">
        <ModalBody>
          <div className="text-center catModalHeader font16px">
            <img
              className="img-fluid"
              referrerPolicy="no-referrer"
              alt="..."
              src={SKILL_IMAGE}
            />
            <br/>
            <strong>{SKILL_NAME}</strong>
          </div>
          <Table bordered className="catModalTable catSkillModalTable">
            <thead>
              <tr>
                <th colSpan="3" className="font14px bc-mediumslateblue">Normal</th>
              </tr>
              <tr className="catSkillWidth">
                <th className="font13px">Level</th>
                <th className="font13px">Conditions</th>
                <th className="font13px">Effect</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>1</th>
                <td>{SKILL_LEVEL_1[0]}</td>
                <td>{SKILL_LEVEL_1[1]}</td>
              </tr>
              <tr>
                <th>2</th>
                <td>{SKILL_LEVEL_2[0]}</td>
                <td>{SKILL_LEVEL_2[1]}</td>
              </tr>
              <tr>
                <th>3</th>
                <td>{SKILL_LEVEL_3[0]}</td>
                <td>{SKILL_LEVEL_3[1]}</td>
              </tr>
            </tbody>
            <thead>
              <tr>
                <th colSpan="3" className="font14px bc-mediumslateblue">Operation Siren</th>
              </tr>
              <tr>
                <th className="font13px">Level</th>
                <th className="font13px">Conditions</th>
                <th className="font13px">Effect</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>1</th>
                <td>{SKILL_OPSI_1[0]}</td>
                <td>{SKILL_OPSI_1[1]}</td>
              </tr>
              <tr>
                <th>2</th>
                <td>{SKILL_OPSI_2[0]}</td>
                <td>{SKILL_OPSI_2[1]}</td>
              </tr>
              <tr>
                <th>3</th>
                <td>{SKILL_OPSI_3[0]}</td>
                <td>{SKILL_OPSI_3[1]}</td>
              </tr>
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
    );
  };
};

MeowfficerSkill.propTypes = propTypes;
