import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class PreloadMedium extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-preloadmedium" className="dps-pm">PM</th>
        <UncontrolledTooltip placement="top" target="templateHead-preloadmedium" delay={0}><b>Preload DPS vs Medium Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
