import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Link } from "react-router-dom";

// Core Components
// import Navigator from "../../components/Navigator/Navigator.js";
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

// Extend Components
import EquipmentHeader from "./EquipmentProfile/EquipmentHeader.js";
import EquipmentMisc   from "./EquipmentProfile/EquipmentMisc.js";
import EquipmentFits   from "./EquipmentProfile/EquipmentFits.js";

const propTypes = { equipmentData: PropTypes.object };

export default class EquipmentProfile extends Component
{
  render()
  {
    // Main
    const EQUIPMENT_DATA = this.props.equipmentData;

    // Metadata
    const EQUIPMENT_IMAGE = EQUIPMENT_DATA.image;
    // const equipment_wikiUrl  = equipmentData.wikiUrl;
    // const equipment_faction  = equipmentData.nationality;
    // const equipment_category = equipmentData.category;

    // Metadata Array
    const EQUIPMENT_NAMES = EQUIPMENT_DATA.names;
    const EQUIPMENT_MISC  = EQUIPMENT_DATA.misc;
    // const equipment_type  = equipmentData.type;
    // const equipment_tiers = equipmentData.tiers;
    const EQUIPMENT_FITS = EQUIPMENT_DATA.fits;

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle">
            <div className="content">
              <EquipmentHeader
                names={EQUIPMENT_NAMES}
                image={EQUIPMENT_IMAGE}
              />
              <EquipmentMisc misc={EQUIPMENT_MISC} />
              <EquipmentFits fits={EQUIPMENT_FITS} />
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

EquipmentProfile.propTypes = propTypes;
