import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table, Modal, ModalBody } from "reactstrap";

import MeowfficerTalentTemplate from "./MeowfficerTalentTemplate";

const propTypes = {
  talentModalOpen: PropTypes.bool,
  talentModalClose: PropTypes.func,
  talent: PropTypes.object
};

export default class MeowfficerTalentModal extends Component
{
  render()
  {
	  const TALENT_MODAL_OPEN	 = this.props.talentModalOpen;
	  const TALENT_MODAL_CLOSE = this.props.talentModalClose;

    let talent_type      = this.props.talent.talentType;
    let talent_primary   = this.props.talent.primary;
    let talent_secondary = this.props.talent.secondary;
    let talent_other     = this.props.talent.other;

    if (talent_type === null || talent_type === undefined)
    {
      talent_type      = "Default";
      talent_primary   = ["flame"];
      talent_secondary = ["flame"];
      talent_other     = ["flame"];
    };

    return (
      <Modal isOpen={TALENT_MODAL_OPEN} toggle={TALENT_MODAL_CLOSE} size="xl" className="MeowfficerTalentModal">
        <ModalBody>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th colSpan="2" className="font16px bc-palegoldenrod">{talent_type}</th>
              </tr>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Primary</th>
              </tr>
            </thead>
            <tbody>
              {talent_primary.map((primary, primaryKey) =>
              {
                const PRIMARY_KEYS = primaryKey + talent_type + "Primary";
                return (
                  <MeowfficerTalentTemplate
                    key={PRIMARY_KEYS}
                    talentID={primary}
                  />
                );
              })}
            </tbody>
            <thead>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Secondary</th>
              </tr>
            </thead>
            <tbody>
              {talent_secondary.map((secondary, secondaryKey) =>
              {
                const SECONDARY_KEYS = secondaryKey + talent_type + "Secondary";
                return (
                  <MeowfficerTalentTemplate
                    key={SECONDARY_KEYS}
                    talentID={secondary}
                  />
                );
              })}
            </tbody>
            <thead>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Other</th>
              </tr>
            </thead>
            <tbody>
              {talent_other.map((other, otherKey) =>
              {
                const OTHER_KEYS = otherKey + talent_type + "Other";
                return (
                  <MeowfficerTalentTemplate
                    key={OTHER_KEYS}
                    talentID={other}
                  />
                );
              })}
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
	  );
  };
};

MeowfficerTalentModal.propTypes = propTypes;
