import React, { Component } from "react";
import PropTypes from "prop-types";
import { Alert } from "reactstrap";

// Core Components
import Navigator  from "../../components/Navigator/Navigator.js";
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

import Mobile  from "./EquipmentType/Mobile.js";
import Desktop from "./EquipmentType/Desktop.js";

// function
import highestRarity from "../../components/highestRarity.js";

const propTypes = {
  navigate: PropTypes.any,
  azurAPI: PropTypes.any,
  type: PropTypes.string
};

export default class EquipmentType extends Component
{
  render()
  {
    let navigate  = this.props.navigate;
    const AZURAPI = highestRarity(this.props.azurAPI);
    const MATCH_TYPE_LIST = this.props.type;

    let equipment_type;
    if (MATCH_TYPE_LIST === "destroyer_guns")
      equipment_type = "Destroyer Guns";

    if (MATCH_TYPE_LIST === "light_cruiser_guns")
      equipment_type = "Light Cruiser Guns";

    if (MATCH_TYPE_LIST === "heavy_cruiser_guns")
      equipment_type = "Heavy Cruiser Guns";

    if (MATCH_TYPE_LIST === "large_cruiser_guns")
      equipment_type = "Large Cruiser Guns";

    if (MATCH_TYPE_LIST === "battleship_guns")
      equipment_type = "Battleship Guns";

    if (MATCH_TYPE_LIST === "ship_torpedoes")
      equipment_type = "Ship Torpedoes";

    if (MATCH_TYPE_LIST === "submarine_torpedoes")
      equipment_type = "Submarine Torpedoes";

    if (MATCH_TYPE_LIST === "fighter_planes")
      equipment_type = "Fighter Planes";

    if (MATCH_TYPE_LIST === "dive_bomber_planes")
      equipment_type = "Dive Bomber Planes";

    if (MATCH_TYPE_LIST === "torpedo_bomber_planes")
      equipment_type = "Torpedo Bomber Planes";

    if (MATCH_TYPE_LIST === "seaplanes")
      equipment_type = "Seaplanes";

    if (MATCH_TYPE_LIST === "anti-air_guns")
      equipment_type = "Anti-Air Guns";

    if (MATCH_TYPE_LIST === "anti-submarine_equipment")
      equipment_type = "Anti-Submarine Equipment";

    if (MATCH_TYPE_LIST === "auxiliary_equipment")
      equipment_type = "Auxiliary Equipment";

    let equipment_list = [];
    AZURAPI.forEach((gear) => { if (gear.category === equipment_type) equipment_list.push(gear); });

    equipment_list.sort((a, b) => b.highest_rarity - a.highest_rarity);

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle">
            <div className="content">
              <Navigator equipment path={equipment_type} />
              <Alert color="warning">
                This page is under Development!
              </Alert>
              <Mobile navigate={navigate} equipmentList={equipment_list} />
              <Desktop navigate={navigate} type={MATCH_TYPE_LIST} equipmentList={equipment_list} />
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

EquipmentType.propTypes = propTypes;
