import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Stats extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-stats">Stats</th>
        <UncontrolledTooltip placement="top" target="templateHead-stats" delay={0}><b>Equipment Stats Bonus</b></UncontrolledTooltip>
      </>
    );
  };
};
