import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Heavy extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-heavys" className="dps-h">H</th>
        <UncontrolledTooltip placement="top" target="templateHead-heavys" delay={0}><b>DPS vs Heavy Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
