import React, { Component } from "react";
import PropTypes from "prop-types";
import { Col, NavLink, Card, CardText } from "reactstrap";

const propTypes = {
  tag: PropTypes.object,
  to: PropTypes.string,
  title: PropTypes.string
};

export default class IndexCard extends Component
{
  render()
  {
    const LINK    = this.props.tag;
    const LINK_TO = this.props.to;
    const TITLE   = this.props.title;
    return (
      <Col xs="6">
        <div>
          <NavLink tag={LINK} to={LINK_TO}>
            <Card body outline color="primary">
              <CardText className="font-weight-bold">{TITLE}</CardText>
            </Card>
          </NavLink>
        </div>
      </Col>
    );
  };
};

IndexCard.propTypes = propTypes;
