import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Link } from "react-router-dom";
import { Table } from "reactstrap";

// components
import TableHead from "./Desktop/TableHead.js";
import TableBody from "./Desktop/TableBody.js";

const propTypes = {
  navigate: PropTypes.any,
  type: PropTypes.string,
  equipmentList: PropTypes.array
};

export default class Desktop extends Component
{
  render()
  {
    const NAVIGATE = this.props.navigate;
    const EQUIPMENT_TYPE = this.props.type;
    const EQUIPMENT_LIST = this.props.equipmentList;
    return (
      <Table className="desktop-view">
        <TableHead type={EQUIPMENT_TYPE} />
        <TableBody type={EQUIPMENT_TYPE} list={EQUIPMENT_LIST} navigate={NAVIGATE} />
      </Table>
    );
  };
};

Desktop.propTypes = propTypes;
