import React, { Component } from "react";
import PropTypes from "prop-types";
import { Alert, Table } from "reactstrap";

// core components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

const propTypes = {
  datamine: PropTypes.any,
  openModal: PropTypes.func
};

export default class DatamineList extends Component
{
  render()
  {
    const DATAMINE = this.props.datamine;

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <Alert color="success" className="text-center align-middle">
            Click to open ship detail
          </Alert>
          <Alert color="danger" className="text-center align-middle">
            Translated with DeepL
          </Alert>
          <div className="main text-center align-middle">
            <div className="content">
              <Table>
                <thead>
                  <tr>
                    <th>ID GROUP</th>
                    <th>SHIP</th>
                    <th>RARITY</th>
                    <th>TYPE</th>
                    <th>FACTION</th>
                  </tr>
                </thead>
                <tbody>
                  {DATAMINE.map((data, key) =>
                  {
                    const SHIP_ID      = data.id.toString().slice(0, -1);
                    const CODE_NAME    = data.code;
                    const SHIP_NAME    = data.name;
                    const SHIP_NAME_EN = data.name_en;
                    const SHIP_TYPE    = data.hullType;
                    const SHIP_FACTION = data.faction;
                    const SHIP_RARITY  = data.rarity;

                    const openModal = () => { this.props.openModal(data) };
                    return (
                      <tr key={key} onClick={openModal} >
                        <td>{SHIP_ID}</td>
                        <td>{CODE_NAME}<br/>{SHIP_NAME}<br/><i>{SHIP_NAME_EN}</i></td>
                        <td>{SHIP_RARITY}</td>
                        <td>{SHIP_TYPE}</td>
                        <td>{SHIP_FACTION}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

DatamineList.propTypes = propTypes;
