function getOathReload(ship_stats, level, kai = false)
{
  if (kai)
  {
    const RELOAD   = ship_stats.retrofit_stats.reload;
    const GROWTH   = ship_stats.retrofit_growth.reload;
    const ENHANCE  = ship_stats.retrofit_enhance.reload;
    const RETROFIT = ship_stats.retrofit.reload;

    return RETROFIT + Math.floor((RELOAD + ((GROWTH * (level - 1)) / 1000) + ENHANCE) * 1.12);
  };

  const RELOAD  = ship_stats.stats.slice(-1)[0].reload;
  const GROWTH  = ship_stats.growth.slice(-1)[0].reload;
  const ENHANCE = ship_stats.enhance.reload;

  return Math.floor((RELOAD + ((GROWTH * (level - 1)) / 1000) + ENHANCE) * 1.12);
};

export default function getShipReload(data_build, stats)
{
  const SHIP_DATA   = data_build.azurapi;
  const SHIP_ID     = data_build.id;
  const SHIP_RELOAD = data_build.reload;
  const SHIP_KAI    = data_build.kai;
  const SHIP_LEVEL  = data_build.level;
  const SHIP_OATH   = data_build.oath;

  const STATS_DATA = stats.find(idx => idx.id === SHIP_ID);
  let return_value = 0;

  if (!SHIP_DATA || !STATS_DATA) return SHIP_RELOAD;

  if (SHIP_KAI.status)
  {
    if (SHIP_LEVEL === "125") return_value = SHIP_DATA.stats.level125Retrofit.reload;
    if (SHIP_LEVEL === "120") return_value = SHIP_DATA.stats.level120Retrofit.reload;
    if (SHIP_LEVEL === "100") return_value = SHIP_DATA.stats.level100Retrofit.reload;
    if (SHIP_OATH)
    {
      if (SHIP_LEVEL === "125") return_value = getOathReload(STATS_DATA, 125, true);
      if (SHIP_LEVEL === "120") return_value = getOathReload(STATS_DATA, 120, true);
      if (SHIP_LEVEL === "100") return_value = getOathReload(STATS_DATA, 100, true);
    };
  }
  else
  {
    if (SHIP_LEVEL === "125") return_value = SHIP_DATA.stats.level125.reload;
    if (SHIP_LEVEL === "120") return_value = SHIP_DATA.stats.level120.reload;
    if (SHIP_LEVEL === "100") return_value = SHIP_DATA.stats.level100.reload;
    if (SHIP_OATH)
    {
      if (SHIP_LEVEL === "125") return_value = getOathReload(STATS_DATA, 125);
      if (SHIP_LEVEL === "120") return_value = getOathReload(STATS_DATA, 120);
      if (SHIP_LEVEL === "100") return_value = getOathReload(STATS_DATA, 100);
    }
  };
  return return_value;
};
