import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Accuracy extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-accuracy">Acc</th>
        <UncontrolledTooltip placement="top" target="templateHead-accuracy" delay={0}><b>Accuracy</b></UncontrolledTooltip>
      </>
    );
  };
};
