export default function factionClass(nationality)
{
  if (nationality === "Eagle Union")
    return "faction-USS";

  if (nationality === "Royal Navy")
    return "faction-HMS";

  if (nationality === "Sakura Empire")
    return "faction-IJN";

  if (nationality === "Iron Blood")
    return "faction-KMS";

  if (nationality === "Dragon Empery")
    return "faction-DE";

  if (nationality === "Northern Parliament")
    return "faction-SN";

  if (nationality === "Iris Libre")
    return "faction-FFNF";

  if (nationality === "Vichya Dominion")
    return "faction-MNF";

  if (nationality === "Sardegna Empire")
    return "faction-RN";

  if (nationality === "Neptunia")
    return "faction-HDN";

  if (nationality === "Bilibili")
    return "faction-bili";

  if (nationality === "Utawarerumono")
    return "faction-collab";

  if (nationality === "KizunaAI")
    return "faction-collab";

  if (nationality === "Hololive")
    return "faction-collab";

  if (nationality === "Venus Vacation")
    return "faction-collab";

  if (nationality === "META")
    return "faction-META";

  return "faction-Universal";
};
