import React, { Component } from "react";
import PropTypes from "prop-types";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

import FleetSlot      from "./FleetBuild/FleetSlot.js";
import FleetModal     from "./FleetBuild/FleetModal.js";
import EquipmentModal from "./FleetBuild/EquipmentModal.js";

// function
import shipGet from "../../components/shipGet.js";

const propTypes = {
  azurAPI: PropTypes.any,
  shipBanner: PropTypes.any,
  stats: PropTypes.any,
  gear: PropTypes.any,
  equip: PropTypes.any,
  openShipModal: PropTypes.func,
  openGearModal: PropTypes.func,
  modalData: PropTypes.object,
  modalOpen: PropTypes.bool,
  modalClose: PropTypes.func,
  slot: PropTypes.number,
  gearData: PropTypes.object,
  gearOpen: PropTypes.bool,
  gearClose: PropTypes.func,
  gearSlot: PropTypes.object,
  shipChange: PropTypes.func
};

export default class FleetBuild extends Component
{
  render()
  {
    const AZURAPI = shipGet(this.props.azurAPI);
    const BANNER  = this.props.shipBanner;
    const STATS   = this.props.stats;
    const GEAR    = this.props.gear;
    const EQUIP   = this.props.equip;

    const OPEN_SHIP_MODAL = this.props.openShipModal;
    const OPEN_GEAR_MODAL = this.props.openGearModal;

    const MODAL_DATA  = this.props.modalData;
    const MODAL_OPEN  = this.props.modalOpen;
    const MODAL_CLOSE = this.props.modalClose;
    const SLOT        = this.props.slot;

    const GEAR_DATA  = this.props.gearData;
    const GEAR_OPEN  = this.props.gearOpen;
    const GEAR_CLOSE = this.props.gearClose;
    const GEAR_SLOT  = this.props.gearSlot;

    const SHIP_CHANGE = this.props.shipChange;
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle shipheader">
            <div className="content">
              <h1>Fleet</h1>
              <div>
                <FleetSlot
                  azurapi={AZURAPI}
                  banner={BANNER}
                  modalData={MODAL_DATA}
                  openShipModal={OPEN_SHIP_MODAL}
                />
                <FleetModal
                  azurapi={AZURAPI}
                  banner={BANNER}
                  stats={STATS}
                  gear={GEAR}
                  modalData={MODAL_DATA}
                  modalOpen={MODAL_OPEN}
                  modalClose={MODAL_CLOSE}
                  slot={SLOT}
                  openGearModal={OPEN_GEAR_MODAL}
                  gearClose={GEAR_CLOSE}
                  gearData={GEAR_DATA}
                  gearSlot={GEAR_SLOT}
                  shipChange={SHIP_CHANGE}
                />
                <EquipmentModal
                  equip={EQUIP}
                  slot={SLOT}
                  gear={GEAR}
                  gearData={GEAR_DATA}
                  gearOpen={GEAR_OPEN}
                  gearClose={GEAR_CLOSE}
                  gearSlot={GEAR_SLOT}
                />
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

FleetBuild.propTypes = propTypes;
