import React, { Component } from "react";
import PropTypes from "prop-types";
import { Input } from "reactstrap";

const propTypes = {
  defaultValue: PropTypes.string,
  onChange: PropTypes.func,
  azurapi: PropTypes.any
};

export default class Pick extends Component
{
  render()
  {
    const VALUE   = this.props.defaultValue;
    const CHANGE  = this.props.onChange;
    const AZURAPI = this.props.azurapi;

    return (
      <Input
        type="select"
        name="ship-select"
        id="ship-select"
        className="input_max"
        defaultValue={VALUE}
        onChange={CHANGE}
        valid
      >
        <option value="000">Unknown</option>
        {AZURAPI.map((ship, key) =>
        {
          return (<option key={key} value={ship.id}>{ship.names.en}</option>);
        })}
      </Input>
    );
  };
};

Pick.propTypes = propTypes;
