import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Speed extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-speed">Spd</th>
        <UncontrolledTooltip placement="top" target="templateHead-speed" delay={0}><b>Speed</b></UncontrolledTooltip>
      </>
    );
  };
};
