import React, { Component } from "react";
import PropTypes from "prop-types";

const propTypes = { skill: PropTypes.object };

export default class DatamineSkill extends Component
{
  render()
  {
    const SKILL = this.props.skill;

    const SKILL_NAME = SKILL.name;
    const SKILL_TYPE = SKILL.type;
    const SKILL_DESC = SKILL.desc;

    const SKILL_NAME_EN = SKILL.name_en;
    const SKILL_DESC_EN = SKILL.desc_en;

    return (
      <>
        <thead>
          <tr>
            <th className={SKILL_TYPE + "_skill"}>{SKILL_NAME}<br/>{SKILL_NAME_EN}</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{SKILL_DESC}<br/><i>{SKILL_DESC_EN}</i></td>
          </tr>
        </tbody>
      </>
    );
  };
};

DatamineSkill.propTypes = propTypes;
