import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Type extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-type">Type</th>
        <UncontrolledTooltip placement="top" target="templateHead-type" delay={0}><b>Type</b></UncontrolledTooltip>
      </>
    );
  };
};
