import React, { Component } from "react";
import PropTypes from "prop-types";

// Components
import ImgR from "../../../../../components/ImgR.js";

// Functions
import factionClass from "../../../../../components/factionClass.js";
import pointRound   from "../../../../../components/pointRound.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array
};

export default class Plane extends Component
{
  render()
  {
    const NAVIGATE = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;


    return (
      <tbody>
        {EQUIPMENT_LIST.map((equip, key) =>
        {
          const ICON = equip.image;
          const NAME = equip.names.en;

          const FACTION       = equip.nationality;
          const FACTION_CLASS = factionClass(FACTION);

          const EQUIPMENT_RARITY = equip.highest_rarity;
          const EQUIPMENT_LINK   = encodeURIComponent(NAME.replace(/\s+/g, "_"));

          const STATS = equip.stats;
          let firepower = "";
          let accuracy  = "";

          if (STATS.firepower) firepower = STATS.firepower.formatted;
          if (STATS.accuracy) accuracy = STATS.accuracy.formatted;

          const ANTI_AIR     = STATS.antiair.formatted;
          const DAMAGE       = parseInt(STATS.damage.max);
          const RATE_OF_FIRE = STATS.rateOfFire.max;
          const RANGE        = STATS.range.formatted;

          const GUNS_RELOAD  = parseFloat(RATE_OF_FIRE.replace(/[^0-9$.]/g, ""));
          const ANTI_AIR_DPS = pointRound(DAMAGE / (GUNS_RELOAD + 0.5)).toString();

          return (
            <tr key={key} onClick={() => NAVIGATE(EQUIPMENT_LINK)}>
              <td>
                <div className={"m-auto equipment-rarity-" + EQUIPMENT_RARITY}><ImgR src={ICON} className="p-10" /></div>
              </td>
              <td>{NAME}</td>
              <td>{firepower}</td>
              <td>{ANTI_AIR}</td>
              <td>{accuracy}</td>
              <td>{DAMAGE}</td>
              <td>{RATE_OF_FIRE}</td>
              <td>{ANTI_AIR_DPS}</td>
              <td>{RANGE}</td>
              <td className={FACTION_CLASS + " faction-background"}>{FACTION}</td>
            </tr>
          );
        })}
      </tbody>
    );
  };
};

Plane.propTypes = propTypes;
