import hullGet       from "../../../../components/hullGet.js";
import highestRarity from "../../../../components/highestRarity.js";

export default function equipmentGet(gear, gear_type, hull_type)
{
  const SHIP_HULL_TYPE = hullGet(hull_type);
  const GEAR_DATA      = highestRarity(gear);

  let equipment_list = [];
  GEAR_DATA.forEach((api) =>
  {
    let gear_pass = false;
    gear_type.forEach((idx) => { if (idx === api.type.name) gear_pass = true; });
    if (!api.fits[SHIP_HULL_TYPE]) gear_pass = false;
    if (gear_type.length === 0) gear_pass = true;

    if (gear_pass) equipment_list.push(api);
  });

  equipment_list.sort((a, b) => a.names.en.localeCompare(b.names.en));
  equipment_list.sort((a, b) => a.type.name.localeCompare(b.type.name));
  equipment_list.sort((a, b) => b.highest_rarity - a.highest_rarity);

  return equipment_list;
};
