import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

import Aviation_img from "../../../../../assets/AL/Aviation.png";
import ImgR from "../../../../../components/ImgR.js";

export default class Aviation extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-aviation"><ImgR src={Aviation_img} /></th>
        <UncontrolledTooltip placement="top" target="templateHead-aviation" delay={0}><b>Aviation</b></UncontrolledTooltip>
      </>
    );
  };
};
