import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Equipment extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-equipment">Equipment</th>
        <UncontrolledTooltip placement="top" target="templateHead-equipment" delay={0}><b>Equipment Name</b></UncontrolledTooltip>
      </>
    );
  };
};
