import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

import factionClass from "../../../components/factionClass.js";

import statAviation  from "../../../assets/AL/Aviation.png";
import statFirepower from "../../../assets/AL/Firepower.png";
import statReload    from "../../../assets/AL/Reload.png";
import statTorpedo   from "../../../assets/AL/Torpedo.png";
import resourceCoin  from "../../../assets/AL/Coin.png";
import resourceMedal from "../../../assets/AL/Medal.png";

const propTypes = {
  retrofit: PropTypes.bool,
  retrofitId: PropTypes.string,
  retrofitHullType: PropTypes.string,
  id: PropTypes.string,
  classType: PropTypes.string,
  nationality: PropTypes.string,
  hullType: PropTypes.string,
  rarity: PropTypes.string,
  constructionTime: PropTypes.string,
  obtainedFrom: PropTypes.string,
  exist: PropTypes.object,
  availableIn: PropTypes.object,
  enhanceValue: PropTypes.object,
  scrapValue: PropTypes.object
};

export default class ShipMetadata extends Component
{
  render()
  {
    const RETROFIT           = this.props.retrofit;
    const RETROFIT_ID        = this.props.retrofitId;
    const RETROFIT_HULL_TYPE = this.props.retrofitHullType;

    // Metadata
    const SHIP_ID = this.props.id;

    const CLASS_TYPE  = this.props.classType;
    const NATIONALITY = this.props.nationality;
    const HULL_TYPE   = this.props.hullType;
    const RARITY      = this.props.rarity;

    const CONSTRUCTION_TIME = this.props.constructionTime;
    const OBTAINED_FROM     = this.props.obtainedFrom;

    const FACTION_CLASS = factionClass(NATIONALITY);
    let rarity_color    = null;

    if (RARITY === "Normal")
      rarity_color = "rarity-text-1";

    if (RARITY === "Rare")
      rarity_color = "rarity-text-2";

    if (RARITY === "Elite")
      rarity_color = "rarity-text-3";

    if (RARITY === "Super Rare" || RARITY === "Priority")
      rarity_color = "rarity-text-4";

    if (RARITY === "Ultra Rare" || RARITY === "Decisive")
      rarity_color = "rarity-text-5";

    let id_format        = SHIP_ID;
    let hull_type_format = HULL_TYPE;
    let rarity_format    = RARITY;
    if (RETROFIT)
    {
      id_format = SHIP_ID + " → " + RETROFIT_ID;
      if (RETROFIT_HULL_TYPE !== HULL_TYPE) hull_type_format = HULL_TYPE + " → " + RETROFIT_HULL_TYPE;

      if (RARITY === "Normal")
      {
        rarity_format = RARITY + " →  Rare";
        rarity_color  = "rarity-gradient-1";
      };

      if (RARITY === "Rare")
      {
        rarity_format = RARITY + " →  Elite";
        rarity_color  = "rarity-gradient-2";
      };

      if (RARITY === "Elite")
      {
        rarity_format = RARITY + " →  Super Rare";
        rarity_color  = "rarity-gradient-3";
      };

      if (RARITY === "Super Rare")
      {
        rarity_format = RARITY + " →  Ultra Rare";
        rarity_color  = "rarity-gradient-4";
      };
    };

    // Metadata with Child
    // exist child        => {en, cn, jp, kr}                            (type: string)
    // availableIn child  => {light, heavy, aviation, limited, exchange} (type: bool, string)
    // enhanceValue child => {firepower, torpedo, aviation, reload}      (type: number)
    // scrapValue child   => {coin, oil, medal}                          (type: number)
    const EXIST         = this.props.exist;
    const AVAILABLE_IN  = this.props.availableIn;
    const ENHANCE_VALUE = this.props.enhanceValue;
    const SCRAP_VALUE   = this.props.scrapValue;

    // exist
    const existMarks  = (existBool) => { if (existBool) return "✔"; return "✘" };
    const EXIST_EN    = existMarks(EXIST.en);
    const EXIST_CN    = existMarks(EXIST.cn);
    const EXIST_JP    = existMarks(EXIST.jp);
    const EXIST_KR    = existMarks(EXIST.kr);
    const existFormat = `EN:${EXIST_EN} | CN:${EXIST_CN} | JP:${EXIST_JP} | KR:${EXIST_KR}`;

    // availableIn
    const availMarks     = (type, availBool) => { if (availBool) return type + ":✔"; return false };
    const AVAIL_LIGHT    = availMarks("Light", AVAILABLE_IN.light);
    const AVAIL_HEAVY    = availMarks("Heavy", AVAILABLE_IN.heavy);
    const AVAIL_AVIATION = availMarks("Aviation", AVAILABLE_IN.aviation);
    const AVAIL_LIMITED  = availMarks("Limited", AVAILABLE_IN.limited);
    const AVAIL_EXCHANGE = availMarks("Medal", AVAILABLE_IN.exchange);

    const AVAIL_IN_ARRAY = [AVAIL_LIGHT, AVAIL_HEAVY, AVAIL_AVIATION, AVAIL_LIMITED, AVAIL_EXCHANGE].filter(Boolean).join(" | ");
    let available_in_format = null;

    if (AVAIL_IN_ARRAY) available_in_format = AVAIL_IN_ARRAY;
    else available_in_format = "-";

    // enhanceValue
    let enhance_value_format = null;
    let enhance_value_render = null;
    if (ENHANCE_VALUE)
    {
      const enhanceDom  = (args, args2) => <><img src={args} referrerPolicy="no-referrer" alt="..." className="img-fluid"/>{args2}</>;
      const FIREPOWER   = enhanceDom(statFirepower, ENHANCE_VALUE.firepower);
      const TORPEDO     = enhanceDom(statTorpedo, ENHANCE_VALUE.torpedo);
      const AVIATION    = enhanceDom(statAviation, ENHANCE_VALUE.aviation);
      const RELOAD      = enhanceDom(statReload, ENHANCE_VALUE.reload);
      enhance_value_format = (<>{FIREPOWER} | {TORPEDO} | {AVIATION} | {RELOAD}</>);
    }

    // scrapValue
    let scrap_value_format = null;
    let scrap_value_render = null;
    if (SCRAP_VALUE)
    {
      // no oil after q4 2021 patch
      const COIN  = SCRAP_VALUE.coin;
      const MEDAL = SCRAP_VALUE.medal;
      let coin_format  = null;
      let modal_format = null;
      if (COIN) coin_format = (<><img src={resourceCoin} referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {COIN}</>);
      if (MEDAL) modal_format = (<> | <img src={resourceMedal} referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {MEDAL}</>);
      scrap_value_format = (<>{coin_format}{modal_format}</>);
    }

    // enhanceValue & scrapValue render
    if (enhance_value_format)
    {
      let scrap_status = false;
      if (scrap_value_format) scrap_status = true;
      const BLANK_VALUE_RENDER = (
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th/>
                <th/>
              </tr>
            </thead>
          </Table>
        </div>
      );
      enhance_value_render = (
        <>
          <div className="dis-inlinetable shipMetadata">
            <Table>
              <thead>
                <tr>
                  <th>Enhance</th>
                  <th>{enhance_value_format}</th>
                </tr>
              </thead>
            </Table>
          </div>
          {scrap_status ? null : BLANK_VALUE_RENDER}
        </>
      );
    }
    if (scrap_value_format)
    {
      scrap_value_render = (
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>Scrap</th>
                <th>{scrap_value_format}</th>
              </tr>
            </thead>
          </Table>
        </div>
      );
    }

    // obtainedFormat
    let obtained_from_format = OBTAINED_FROM;
    if (OBTAINED_FROM) obtained_from_format = OBTAINED_FROM.split("\n").map((line, i) => (<span key={i}>{line}<br/></span>));

    return (
      <div className={FACTION_CLASS + " faction-background"}>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>ID</th>
                <th>{id_format}</th>
              </tr>
              <tr>
                <th>Class</th>
                <th>{CLASS_TYPE}</th>
              </tr>
              <tr>
                <th>Faction</th>
                <th>{NATIONALITY}</th>
              </tr>
              <tr>
                <th>Classification</th>
                <th>{hull_type_format}</th>
              </tr>
            </thead>
          </Table>
        </div>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>Rarity</th>
                <th className={rarity_color}>{rarity_format}</th>
              </tr>
              <tr>
                <th>Construction</th>
                <th>{CONSTRUCTION_TIME}</th>
              </tr>
              <tr>
                <th>Exist</th>
                <th>{existFormat}</th>
              </tr>
              <tr>
                <th>Available</th>
                <th>{available_in_format}</th>
              </tr>
            </thead>
          </Table>
        </div>
        {enhance_value_render}
        {scrap_value_render}
        <div>
          <Table>
            <thead>
              <tr>
                <th>{obtained_from_format}</th>
              </tr>
            </thead>
          </Table>
        </div>
      </div>
    );
  };
};

ShipMetadata.propTypes = propTypes;
