import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Spread extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-spread">Sprd</th>
        <UncontrolledTooltip placement="top" target="templateHead-spread" delay={0}><b>Spread</b></UncontrolledTooltip>
      </>
    );
  };
};
