import React, { useState } from "react";

// core components
import Navbar     from "../components/Navbar/Navbar.js";
import PageHeader from "../components/PageHeader/PageHeader.js";
import Footer     from "../components/Footer/Footer.js";

// meowfficer component
import MeowfficerAllTalentModal from "./Meowfficer/MeowfficerAllTalentModal";
import MeowfficerList           from "./Meowfficer/MeowfficerList";
import MeowfficerModal          from "./Meowfficer/MeowfficerModal";

// json
import MeowfficerData   from "../assets/json/MeowfficerData.json";
import MeowfficerTalent from "../assets/json/MeowfficerTalent.json";

function Meowfficer()
{
  const [modal, setModal]             = useState(false);
  const [skillModal, setSkillModal]   = useState(false);
  const [allTalent, setAllTalent]     = useState(false);
  const [talentModal, setTalentModal] = useState(false);

  const [modalData, setModalData] = useState({
    name: null,
    codex: null,
    talentType: null,
    primary: null,
    secondary: null,
    other: null
  });

  const [talentData, setTalentData] = useState({
    talentType: null,
    primary: null,
    secondary: null,
    other: null
  });

  // profile modal
  const openModal = (cat) =>
  {
    setModalData({
      name: cat.name,
      codex: cat.codex
    });
    setModal(true);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + cat.name.toLowerCase()
    );
  };

  const closeModal = () =>
  {
    setModal(false);
    window.history.replaceState(null, null, "#/meowfficer");
  };

  // skill modal
  const openSkillModal = () =>
  {
    setSkillModal(true);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase()) + "=skill"
    );
  };

  const closeSkillModal = () =>
  {
    setSkillModal(false);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase())
    );
  };

  // all talent modal
  const openAllTalent = () =>
  {
    setAllTalent(true);
    window.history.replaceState(null, null,
      "#/meowfficer?open=AllTalentList"
    );
  };

  const closeAllTalent = () =>
  {
    setAllTalent(false);
    window.history.replaceState(null, null, "#/meowfficer");
  };

  // talent modal
  const openTalentModal = (talent) =>
  {
    setTalentData({
      talentType: talent.talentType,
      primary: talent.talentPrimary,
      secondary: talent.talentSecondary,
      other: talent.talentOther
    });
    setTalentModal(true);
    const TALENT_NAME = escape(talent.talentType.toLowerCase().replace(/\s+/g, "_"));

    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase()) + "=talent?=" + TALENT_NAME
    );
  };

  const closeTalentModal = () =>
  {
    setTalentModal(false);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase())
    );
  };

  React.useEffect(() =>
  {
    try
    {
      const URL_LINK = window.location.href.split("=");
      if (URL_LINK.length > 1)
      {
        let cat   = "";
        let codex = 0;

        for (let i = 0; i < MeowfficerData.length; i++)
        {
          if (MeowfficerData[i].name.toLowerCase() === URL_LINK[1])
          {
            cat = MeowfficerData[i];
            codex = i;
          }
        }
        setModalData({
          name: cat.name,
          codex: codex
        });
        setModal(true);
        if (URL_LINK[1] === "AllTalentList") setAllTalent(true);
        if (URL_LINK.length === 3) setSkillModal(true);
        if (URL_LINK.length === 4)
        {
          const TALENT           = MeowfficerTalent[codex];
          const TALENT_COMPARE_1 = escape(TALENT.talent[0].type.toLowerCase().replace(/\s+/g, "_"));
          let talent_compare_2   = "";

          if (TALENT.talent.length === 2)
            talent_compare_2 = escape(TALENT.talent[1].type.toLowerCase().replace(/\s+/g, "_"));

          if (URL_LINK[3] === TALENT_COMPARE_1)
          {
            setTalentData({
              talentType: TALENT.talent[0].type,
              primary: TALENT.talent[0].primary,
              secondary: TALENT.talent[0].secondary,
              other: TALENT.talent[0].other
            });
          };

          if (URL_LINK[3] === talent_compare_2)
          {
            setTalentData({
              talentType: TALENT.talent[1].type,
              primary: TALENT.talent[1].primary,
              secondary: TALENT.talent[1].secondary,
              other: TALENT.talent[1].other
            });
          };

          setTalentModal(true);
        };
      };
    }
    catch (e)
    {
      console.log("Meowfficer not found.");
      window.history.replaceState(null, null, "#/meowfficer");
    }
  }, []);

  return (
    <>
      <Navbar />
      <div className="wrapper">
        <PageHeader />
        <div className="main meowfficerheader">
          <div className="content">
            <h1>Meowfficer</h1>
            <MeowfficerAllTalentModal
              openAllTalent={openAllTalent}
              allTalentOpen={allTalent}
              allTalentClose={closeAllTalent}
            />
            <div className="containerCS">
              {MeowfficerData.map((cat, key) =>
              {
                const name = cat.name;
                const keys = "List" + key + name;
                return (
                  <MeowfficerList
                    key={keys}
                    codex={key}
                    openModal={openModal}
                    name={name}
                  />
                );
              })}
            </div>
            <MeowfficerModal
              modalData={modalData}
              modalOpen={modal}
              modalClose={closeModal}
              openSkillModal={openSkillModal}
              skillModalOpen={skillModal}
              skillModalClose={closeSkillModal}
              openTalentModal={openTalentModal}
              talentData={talentData}
              talentModalOpen={talentModal}
              talentModalClose={closeTalentModal}
            />
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}

export default Meowfficer;
