import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Attribute extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-attribute">Attr</th>
        <UncontrolledTooltip placement="top" target="templateHead-attribute" delay={0}><b>Attribute/Characteristic</b></UncontrolledTooltip>
      </>
    );
  };
};
