import React, { Component } from "react";
import PropTypes from "prop-types";

// Image
import Health    from "../../../../../assets/AL/Health.png";
import Firepower from "../../../../../assets/AL/Firepower.png";
import AntiAir   from "../../../../../assets/AL/AntiAir.png";
import Torpedo   from "../../../../../assets/AL/Torpedo.png";
import Aviation  from "../../../../../assets/AL/Aviation.png";
import Reload    from "../../../../../assets/AL/Reload.png";
import Evasion   from "../../../../../assets/AL/Evasion.png";
import ASW       from "../../../../../assets/AL/ASW.png";
import Oxygen    from "../../../../../assets/AL/Oxygen.png";
import Hit       from "../../../../../assets/AL/Hit.png";

// Component
import ImgR from "../../../../../components/ImgR.js";

const propTypes = {
  navigate: PropTypes.any,
  list: PropTypes.array
};

export default class Auxiliary extends Component
{
  render()
  {
    const NAVIGATE		   = this.props.navigate;
    const EQUIPMENT_LIST = this.props.list;

    return (
      <tbody>
        {EQUIPMENT_LIST.map((equip, key) =>
        {
          const ICON = equip.image;
          const NAME = equip.names.en;

          const EQUIPMENT_RARITY = equip.highest_rarity;
          const EQUIPMENT_LINK   = encodeURIComponent(NAME.replace(/\s+/g, "_"));

          const STATS = equip.stats;

          const format = (stats, type) => <p><ImgR src={type}/><br/>{stats}</p>;

          let health    = <></>;
          let firepower = <></>;
          let antiair   = <></>;
          let torpedo   = <></>;
          let aviation  = <></>;
          let reload    = <></>;
          let evasion   = <></>;
          let asw       = <></>;
          let oxygen    = <></>;
          let hit       = <></>;
          let speed     = <></>;

          // Health
          if (STATS.health)
          {
            if (STATS.health.type === "min_max") health = format(STATS.health.max, Health);
            if (STATS.health.type === "min_mid_max") health = format(STATS.health.mid, Health);
          };

          // Firepower
          if (STATS.firepower)
          {
            if (STATS.firepower.type === "min_max") firepower = format(STATS.firepower.max, Firepower);
            if (STATS.firepower.type === "min_mid_max") firepower = format(STATS.firepower.mid, Firepower);
          };

          // Anti-Air
          if (STATS.antiair)
          {
            if (STATS.antiair.type === "min_max") antiair = format(STATS.antiair.max, AntiAir);
            if (STATS.antiair.type === "min_mid_max") antiair = format(STATS.antiair.mid, AntiAir);
          };

          // Torpedo
          if (STATS.torpedo)
          {
            if (STATS.torpedo.type === "min_max") torpedo = format(STATS.torpedo.max, Torpedo);
            if (STATS.torpedo.type === "min_mid_max") torpedo = format(STATS.torpedo.mid, Torpedo);
          };

          // Aviation
          if (STATS.aviation)
          {
            if (STATS.aviation.type === "min_max") aviation = format(STATS.aviation.max, Aviation);
            if (STATS.aviation.type === "min_mid_max") aviation = format(STATS.aviation.mid, Aviation);
          };

          // Reload
          if (STATS.reload)
          {
            if (STATS.reload.type === "min_max") reload = format(STATS.reload.max, Reload);
            if (STATS.reload.type === "min_mid_max") reload = format(STATS.reload.mid, Reload);
          };

          // Evasion
          if (STATS.evasion)
          {
            if (STATS.evasion.type === "min_max") evasion = format(STATS.evasion.max, Evasion);
            if (STATS.evasion.type === "min_mid_max") evasion = format(STATS.evasion.mid, Evasion);
          };

          // Anti Submarine Warfare
          if (STATS.antiSubmarineWarfare)
          {
            if (STATS.antiSubmarineWarfare.type === "min_max") asw = format(STATS.antiSubmarineWarfare.max, ASW);
            if (STATS.antiSubmarineWarfare.type === "min_mid_max") asw = format(STATS.antiSubmarineWarfare.mid, ASW);
          };

          // Oxygen
          if (STATS.oxygen)
          {
            if (STATS.oxygen.type === "min_max") oxygen = format(STATS.oxygen.max, Oxygen);
            if (STATS.oxygen.type === "min_mid_max") oxygen = format(STATS.oxygen.mid, Oxygen);
          };

          // Accuracy
          if (STATS.accuracy)
          {
            if (STATS.accuracy.type === "min_max") hit = format(STATS.accuracy.max, Hit);
            if (STATS.accuracy.type === "min_mid_max") hit = format(STATS.accuracy.mid, Hit);
          };

          // Speed
          if (STATS.spd)
          {
            if (STATS.spd.type === "min_max") speed = (<p><b>SPD</b><br/>{STATS.spd.max}</p>);
            if (STATS.spd.type === "min_mid_max") speed = (<p><b>SPD</b><br/>{STATS.spd.mid}</p>);
          };

          const STARS = equip.stars.stars;
          const NOTES = equip.misc.notes;

          return (
            <tr key={key} onClick={() => NAVIGATE(EQUIPMENT_LINK)}>
              <td>
                <div className={"m-auto equipment-rarity-" + EQUIPMENT_RARITY}><ImgR src={ICON} className="p-10" /></div>
              </td>
              <td>{NAME}</td>
              <td>{STARS}</td>
              <td>{health}{firepower}{antiair}{torpedo}{aviation}{reload}{evasion}{asw}{oxygen}{hit}{speed}</td>
              <td>{NOTES}</td>
            </tr>
          );
        })}
      </tbody>
    );
  };
};

Auxiliary.propTypes = propTypes;
