import React from "react";
import { Link } from "react-router-dom";
import { Alert, Row } from "reactstrap";

// core components
import Navbar     from "../components/Navbar/Navbar.js";
import PageHeader from "../components/PageHeader/PageHeader.js";
import Footer     from "../components/Footer/Footer.js";

import IndexCard from "./Index/IndexCard.js";

export default function Index()
{
  React.useEffect(() =>
  {
    document.body.classList.toggle("landing-page");
    // Specify how to clean up after this effect:
    return function cleanup()
    {
      document.body.classList.toggle("landing-page");
    };
  }, []);

  return (
    <>
      <Navbar />
      <div className="wrapper">
        <PageHeader />
        <Alert color="warning" className="text-center align-middle">
          This page is under Development!
        </Alert>
        <div className="main text-center align-middle">
          <div className="content-center">
            <Row className="row-grid justify-content-between align-items-center">
              <IndexCard tag={Link} to="/ship" title="Ship" />
              <IndexCard tag={Link} to="/meowfficer" title="Meowfficer" />
              <IndexCard tag={Link} to="/equipment" title="Equipment" />
              <IndexCard tag={Link} to="/datamine" title="Datamine" />
              <IndexCard tag={Link} to="/fleet" title="Fleet" />
              <IndexCard tag={Link} to="/meowfficer?open=justice" title="[TEST] - open justice" />
              <IndexCard tag={Link} to="/meowfficer?open=justice=talent?=destroyer_gun" title="[TEST] - open justice/talent" />
              <IndexCard tag={Link} to="/ship/shinano" title="[TEST] - open ship/shinano" />
              <IndexCard tag={Link} to="/" title="Blank" />
              <IndexCard tag={Link} to="/" title="Blank" />
              <IndexCard tag={Link} to="/" title="Blank" />
              <IndexCard tag={Link} to="/" title="Blank" />
            </Row>
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}
