import React, { Component } from "react";
import PropTypes from "prop-types";
import { Input } from "reactstrap";

const propTypes = {
  inputReload: PropTypes.func,
  inputTech: PropTypes.func,
  inputCat: PropTypes.func,
  reload: PropTypes.number,
  tech: PropTypes.number,
  cat: PropTypes.number,
  finalReload: PropTypes.number
};

export default class Reload extends Component
{
  render()
  {
    const INPUT_RELOAD = this.props.inputReload;
    const INPUT_TECH   = this.props.inputTech;
    const INPUT_CAT    = this.props.inputCat;

    const RELOAD = this.props.reload;
    const TECH   = this.props.tech;
    const CAT    = this.props.cat;

    const FINAL_RELOAD = this.props.finalReload;

    return (
      <thead>
        <tr>
          <th>BASE RELOAD</th>
          <th>FLEET TECH RELOAD</th>
          <th>MEOWFFICER RELOAD</th>
          <th>FINAL RELOAD</th>
        </tr>
        <tr>
          <th>
            <Input
              id="reload-base"
              type="number" min="0"
              className="fleet-input"
              onChange={INPUT_RELOAD}
              defaultValue={RELOAD}
            />
          </th>
          <th>
            <Input
              id="reload-tech"
              type="number" min="0"
              className="fleet-input"
              onChange={INPUT_TECH}
              defaultValue={TECH}
            />
          </th>
          <th>
            <Input
              id="reload-cat"
              type="number" min="0"
              className="fleet-input"
              onChange={INPUT_CAT}
              defaultValue={CAT}
            />
          </th>
          <th>
            <Input
              id="reload-final"
              type="number" min="0"
              className="fleet-input-disabled"
              defaultValue={FINAL_RELOAD}
              disabled
            />
          </th>
        </tr>
      </thead>
    );
  };
};

Reload.propTypes = propTypes;
