import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Routes, Navigate } from "react-router-dom";

import "./assets/css/nucleo-icons.css";
import "./assets/scss/cs-react.scss?v=2.0.0";
import "./assets/css/style.css";

// parent element
import Index      from "./views/Index.js";
import Ship       from "./views/Ship.js";
import Meowfficer from "./views/Meowfficer.js";
import Equipment  from "./views/Equipment.js";
import Datamine   from "./views/Datamine.js";
import Fleet      from "./views/Fleet.js";

ReactDOM.render(
  <HashRouter>
    <Routes>
      <Route
        path="/home"
        element={<Index />}
      />
      <Route
        path="/ship/*"
        element={<Ship />}
      />
      <Route
        path="/meowfficer"
        element={<Meowfficer />}
      />
      <Route
        path="/equipment/*"
        element={<Equipment />}
      />
      <Route
        path="/datamine"
        element={<Datamine />}
      />
      <Route
        path="/fleet"
        element={<Fleet />}
      />
      <Route path="*" element={<Navigate replace to="/home" />} />
    </Routes>
  </HashRouter>,
  document.getElementById("root")
);
