import React, { Component } from "react";
import PropTypes from "prop-types";
import { Button, Modal, ModalBody, Table } from "reactstrap";

// Stats Icon
import statAntiAir     from "../../assets/AL/AntiAir.png";
import statArmor       from "../../assets/AL/Armor.png";
import statASW         from "../../assets/AL/ASW.png";
import statAviation    from "../../assets/AL/Aviation.png";
import statConsumption from "../../assets/AL/Consumption.png";
import statEvasion     from "../../assets/AL/Evasion.png";
import statFirepower   from "../../assets/AL/Firepower.png";
import statHealth      from "../../assets/AL/Health.png";
import statHit         from "../../assets/AL/Hit.png";
import statLuck        from "../../assets/AL/Luck.png";
import statReload      from "../../assets/AL/Reload.png";
import statTorpedo     from "../../assets/AL/Torpedo.png";

import ImgR from "../../components/ImgR";
import DatamineSkill from "./DatamineModal/DatamineSkill.js";

const propTypes = {
  modalOpen: PropTypes.bool,
  modalClose: PropTypes.func,
  modalData: PropTypes.object,
  setModalData: PropTypes.func
};

export default class DatamineModal extends Component
{
  render()
  {
    const MODAL_OPEN  = this.props.modalOpen;
    const MODAL_CLOSE = this.props.modalClose;
    const MODAL_DATA  = this.props.modalData;

    const CODE_NAME    = MODAL_DATA.code;
    const SHIP_NAME    = MODAL_DATA.name;
    const SHIP_NAME_EN = MODAL_DATA.name_en;
    const SHIP_TYPE    = MODAL_DATA.hullType;
    const SHIP_FACTION = MODAL_DATA.faction;
    const SHIP_RARITY  = MODAL_DATA.rarity;
    const SHIP_SKILL   = MODAL_DATA.skill;

    const LIMIT_BREAK_1 = MODAL_DATA.limitbreak[0];
    const LIMIT_BREAK_2 = MODAL_DATA.limitbreak[1];
    const LIMIT_BREAK_3 = MODAL_DATA.limitbreak[2];

    const LIMIT_BREAK_1_EN = MODAL_DATA.limitbreak_en[0];
    const LIMIT_BREAK_2_EN = MODAL_DATA.limitbreak_en[1];
    const LIMIT_BREAK_3_EN = MODAL_DATA.limitbreak_en[2];

    const EQUIP_SLOT_1 = MODAL_DATA.equip.slot1;
    const EQUIP_SLOT_2 = MODAL_DATA.equip.slot2;
    const EQUIP_SLOT_3 = MODAL_DATA.equip.slot3;

    const HEALTH    = MODAL_DATA.stats.health;
    const ARMOR     = MODAL_DATA.stats.armor;
    const FIREPOWER = MODAL_DATA.stats.firepower;
    const TORPEDO   = MODAL_DATA.stats.torpedo;
    const ANTIAIR   = MODAL_DATA.stats.antiAir;
    const AVIATION  = MODAL_DATA.stats.aviation;
    const RELOAD    = MODAL_DATA.stats.reload;
    const HIT       = MODAL_DATA.stats.hit;
    const EVASION   = MODAL_DATA.stats.evasion;
    const SPEED     = MODAL_DATA.stats.speed;
    const LUCK      = MODAL_DATA.stats.luck;
    const ASW       = MODAL_DATA.stats.asw;
    const COST      = MODAL_DATA.stats.cost;

    return (
      <Modal isOpen={MODAL_OPEN} toggle={MODAL_CLOSE} size="xl" className="datamineModal">
        <ModalBody>
          <h3 className="text-center catModalHeader">
            <strong>{CODE_NAME}<br/>{SHIP_NAME}<br/><i>{SHIP_NAME_EN}</i></strong>
          </h3>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th className="width_50">RARITY</th>
                <th className="width_50">{SHIP_RARITY}</th>
              </tr>
              <tr>
                <th>TYPE</th>
                <th>{SHIP_TYPE}</th>
              </tr>
              <tr>
                <th>FACTION</th>
                <th>{SHIP_FACTION}</th>
              </tr>
              <tr>
                <th>LEVEL</th>
                <th>125</th>
              </tr>
            </thead>
          </Table>
          <Table bordered className="catModalTable">
            <tbody>
              <tr>
                <th className="width_25"><ImgR src={statHealth} /></th>
                <th className="width_25">{HEALTH}</th>
                <th className="width_25"><ImgR src={statArmor} /></th>
                <th className="width_25">{ARMOR}</th>
              </tr>
              <tr>
                <th><ImgR src={statFirepower} /></th>
                <th>{FIREPOWER}</th>
                <th><ImgR src={statTorpedo} /></th>
                <th>{TORPEDO}</th>
              </tr>
              <tr>
                <th><ImgR src={statAntiAir} /></th>
                <th>{ANTIAIR}</th>
                <th><ImgR src={statAviation} /></th>
                <th>{AVIATION}</th>
              </tr>
              <tr>
                <th><ImgR src={statReload} /></th>
                <th>{RELOAD}</th>
                <th><ImgR src={statHit} /></th>
                <th>{HIT}</th>
              </tr>
              <tr>
                <th><ImgR src={statEvasion} /></th>
                <th>{EVASION}</th>
                <th>SPD</th>
                <th>{SPEED}</th>
              </tr>
              <tr>
                <th><ImgR src={statLuck} /></th>
                <th>{LUCK}</th>
                <th><ImgR src={statASW} /></th>
                <th>{ASW}</th>
              </tr>
              <tr>
                <th><ImgR src={statConsumption} /></th>
                <th>{COST}</th>
                <th/>
                <th/>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th>Limit Break</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{LIMIT_BREAK_1}<br/><i>{LIMIT_BREAK_1_EN}</i></td>
              </tr>
              <tr>
                <td>{LIMIT_BREAK_2}<br/><i>{LIMIT_BREAK_2_EN}</i></td>
              </tr>
              <tr>
                <td>{LIMIT_BREAK_3}<br/><i>{LIMIT_BREAK_3_EN}</i></td>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th>SLOT</th>
                <th>COEFFICIENT</th>
                <th>GUN MOUNT</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{EQUIP_SLOT_1.type}</td>
                <td>{EQUIP_SLOT_1.efficiency}</td>
                <td>{EQUIP_SLOT_1.count}</td>
              </tr>
              <tr>
                <td>{EQUIP_SLOT_2.type}</td>
                <td>{EQUIP_SLOT_2.efficiency}</td>
                <td>{EQUIP_SLOT_2.count}</td>
              </tr>
              <tr>
                <td>{EQUIP_SLOT_3.type}</td>
                <td>{EQUIP_SLOT_3.efficiency}</td>
                <td>{EQUIP_SLOT_3.count}</td>
              </tr>
            </tbody>
          </Table>
          <Table bordered className="catModalTable catModalTableAlign">
            <thead>
              <tr>
                <th colSpan="2">Skill</th>
              </tr>
            </thead>
            {SHIP_SKILL.map((skill, key) => <DatamineSkill skill={skill} key={key} />)}
          </Table>
          <div className="mobile-view text-center align-middle">
            <Button color="danger" onClick={MODAL_CLOSE}>Close</Button>
          </div>
        </ModalBody>
      </Modal>
    );
  };
};

DatamineModal.propTypes = propTypes;
