import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table, Modal, ModalBody } from "reactstrap";

import MeowfficerTalent from "../../../assets/json/MeowfficerTalent.json";

import MeowfficerSupport from "../../../assets/AL/MeowfficerSupport.png";
import MeowfficerCommand from "../../../assets/AL/MeowfficerCommand.png";
import MeowfficerTactics from "../../../assets/AL/MeowfficerTactics.png";

const propTypes = {
  modalOpen: PropTypes.bool,
  modalClose: PropTypes.func,
  openSkillModal: PropTypes.func,
  profile: PropTypes.object,
  openTalentModal: PropTypes.func
};

export default class MeowfficerProfile extends Component
{
  render()
  {
    const MODAL_OPEN 		   = this.props.modalOpen;
    const MODAL_CLOSE 		 = this.props.modalClose;
    const OPEN_SKILL_MODAL = this.props.openSkillModal;
    const PROFILE 			 = this.props.profile;

    // profile
    const BANNER 		 = PROFILE.banner;
    const NAME 			 = PROFILE.name;
    const LOGISTICS  = PROFILE.logistics;
    const DIRECTIVES = PROFILE.directives;
    const TACTICS    = PROFILE.tactics;
    const TYPE 			 = PROFILE.type;
    const SLOT 			 = PROFILE.slot;
    const RARITY 		 = PROFILE.rarity;
    const FACTION 	 = PROFILE.faction;
    const BUILD_TIME = PROFILE.timer;
    const SKILL 		 = PROFILE.skill;

    let meowfficer = MeowfficerTalent[0];
	  for (let i = 0; i < MeowfficerTalent.length; i++)
	  {
	    if (MeowfficerTalent[i].name === NAME)
	    {
	      meowfficer = MeowfficerTalent[i];
	     }
	  }

    return (
		  <Modal isOpen={MODAL_OPEN} toggle={MODAL_CLOSE} className="meowfficerModal">
		    <ModalBody>
		      <div className="text-center catModalHeader">
		        <img
		          className="img-fluid"
		          referrerPolicy="no-referrer"
		          alt="..."
		          src={BANNER}
		        />
		        <br/>
		        <strong>{NAME}</strong>
		      </div>
			    <Table bordered className="catModalTable catModalTableAlign">
			      <thead>
			        <tr>
			          <th>
			            <img src={MeowfficerSupport} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Logistics
			          </th>
			          <th>
			            <img src={MeowfficerCommand} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Directives
			          </th>
			          <th>
			            <img src={MeowfficerTactics} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Tactics
			          </th>
			        </tr>
			      </thead>
		        <tbody>
		          <tr>
		            <td>{LOGISTICS}</td>
		            <td>{DIRECTIVES}</td>
		            <td>{TACTICS}</td>
		          </tr>
		          <tr>
		          	<th colSpan="3">Talent</th>
		          </tr>
		          {meowfficer.talent.map((talent, key) =>
	          	{
		          	const TALENT_KEY 			 = NAME + "Talent" + key;
		          	const TALENT_TYPE 		 = talent.type;
		          	const TALENT_PRIMARY 	 = talent.primary;
		          	const TALENT_SECONDARY = talent.secondary;
		          	const TALENT_OTHER 		 = talent.other;
		          	const openTalentModal = () =>
		          	{
		          		this.props.openTalentModal({
		          			talentType: TALENT_TYPE,
		          			talentPrimary: TALENT_PRIMARY,
		          			talentSecondary: TALENT_SECONDARY,
		          			talentOther: TALENT_OTHER
		          		});
		          	};
		          	return (
		          		<tr key={TALENT_KEY} onClick={openTalentModal}>
				          	<th colSpan="3" className="linkCS">{TALENT_TYPE}</th>
				          </tr>
		          	);
		          })}
		          <tr onClick={OPEN_SKILL_MODAL}>
		            <th>Skill</th>
		            <th colSpan="2" className="linkCS">{SKILL}</th>
		          </tr>
		          <tr>
		            <th>Skill Type</th>
		            <td colSpan="2">{TYPE}</td>
		          </tr>
		          <tr>
		            <th>Skill Slot</th>
		            <td colSpan="2">{SLOT}</td>
		          </tr>
		          <tr>
		            <th>Rarity</th>
		            <td colSpan="2">{RARITY}</td>
		          </tr>
		          <tr>
		            <th>Faction</th>
		            <td colSpan="2">{FACTION}</td>
		          </tr>
		          <tr>
		            <th>Training Time</th>
		            <td colSpan="2">{BUILD_TIME}</td>
		          </tr>
		        </tbody>
		      </Table>
		    </ModalBody>
		  </Modal>
    );
  };
};

MeowfficerProfile.propTypes = propTypes;
