import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Rounds extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-rounds">Rnd/s</th>
        <UncontrolledTooltip placement="top" target="templateHead-rounds" delay={0}><b>Round/Second</b></UncontrolledTooltip>
      </>
    );
  };
};
