import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = {
  limitBreaks: PropTypes.bool,
  devBreaks: PropTypes.array
};

export default class ShipBreaks extends Component
{
  render()
  {
    // limitBreaks => {multiple array[]} (type: string)
    // devLevels => {level, buffs(multiple array[])} (type: string)
    const LIMIT_BREAKS = this.props.limitBreaks;
    const DEV_BREAKS   = this.props.devBreaks;

    let limit_breaks_format = null;
    if (LIMIT_BREAKS)
    {
      const LIMIT_BREAK_1 = DEV_BREAKS[0].join(" / ");
      const LIMIT_BREAK_2 = DEV_BREAKS[1].join(" / ");
      const LIMIT_BREAK_3 = DEV_BREAKS[2].join(" / ");

      limit_breaks_format = (
        <>
          <thead>
            <tr>
              <th colSpan="3">Limit Breaks</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>1</th>
              <th>{LIMIT_BREAK_1}</th>
            </tr>
            <tr>
              <th>2</th>
              <th>{LIMIT_BREAK_2}</th>
            </tr>
            <tr>
              <th>3</th>
              <th>{LIMIT_BREAK_3}</th>
            </tr>
          </tbody>
        </>
      );
    }
    else
    {
      const DEV_LEVEL_05 = DEV_BREAKS[0].buffs.join(" / ");
      const DEV_LEVEL_10 = DEV_BREAKS[1].buffs.join(" / ");
      const DEV_LEVEL_15 = DEV_BREAKS[2].buffs.join(" / ");
      const DEV_LEVEL_20 = DEV_BREAKS[3].buffs.join(" / ");
      const DEV_LEVEL_25 = DEV_BREAKS[4].buffs.join(" / ");
      const DEV_LEVEL_30 = DEV_BREAKS[5].buffs.join(" / ");

      limit_breaks_format = (
        <>
          <thead>
            <tr>
              <th colSpan="3">Development Levels</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>5</th>
              <th>{DEV_LEVEL_05}</th>
            </tr>
            <tr>
              <th>10</th>
              <th>{DEV_LEVEL_10}</th>
            </tr>
            <tr>
              <th>15</th>
              <th>{DEV_LEVEL_15}</th>
            </tr>
            <tr>
              <th>20</th>
              <th>{DEV_LEVEL_20}</th>
            </tr>
            <tr>
              <th>25</th>
              <th>{DEV_LEVEL_25}</th>
            </tr>
            <tr>
              <th>30</th>
              <th>{DEV_LEVEL_30}</th>
            </tr>
          </tbody>
        </>
      );
    }

    return (
      <div>
        <Table className="tableCS">
          {limit_breaks_format}
        </Table>
      </div>
    );
  };
};

ShipBreaks.propTypes = propTypes;
