import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = { notes: PropTypes.string };

export default class Notes extends Component
{
  render()
  {
    const equipment_notes = this.props.notes;
    if (equipment_notes !== "")
    {
      return (
        <Table>
          <thead>
            <tr>
              <th>Notes</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{equipment_notes}</td>
            </tr>
          </tbody>
        </Table>
      );
    };
    return null;
  };
};

Notes.propTypes = propTypes;

