import React, { Component } from "react";
import PropTypes from "prop-types";
import { Breadcrumb } from "reactstrap";

// icon load
import "../../assets/css/nucleo-icons.css";

// import part
import Home from "./components/Home.js";
import Ship from "./components/Ship.js";
import Equipment from "./components/Equipment.js";
import Path from "./components/Path.js";

const propTypes = {
  ship: PropTypes.bool,
  equipment: PropTypes.bool,
  path: PropTypes.string
};

export default class Navigator extends Component
{
  render()
  {
    let ship      = this.props.ship;
    let equipment = this.props.equipment;
    let path_link = this.props.path;
    let execute   = true;
    let output    = (<Home />);

    if (ship && equipment) execute = false;
    if (!path_link) execute = false;

    if (ship && execute) output = (<>{output}<Ship /><Path path={path_link} /></>);
    if (equipment && execute) output = (<>{output}<Equipment /><Path path={path_link} /></>);

    return (
      <Breadcrumb>
        {output}
      </Breadcrumb>
    );
  };
};

Navigator.propTypes = propTypes;
