import React, { Component } from "react";
import PropTypes from "prop-types";
import { Modal, ModalBody, Table } from "reactstrap";

import equipmentGet from  "./EquipmentModal/equipmentGet.js";

import ImgR      from "../../../components/ImgR.js";
import rarityGet from "../../../components/rarityGet.js";

const propTypes = {
  equip: PropTypes.any,
  slot: PropTypes.number,
  gear: PropTypes.any,
  gearData: PropTypes.object,
  gearOpen: PropTypes.bool,
  gearClose: PropTypes.func,
  gearSlot: PropTypes.object
};

export default class EquipmentModal extends Component
{
  render()
  {
    const EQUIP      = this.props.equip;
    const SLOT       = this.props.slot;
    const GEAR       = this.props.gear;
    const GEAR_DATA  = this.props.gearData;
    const GEAR_OPEN  = this.props.gearOpen;
    const GEAR_SLOT  = this.props.gearSlot;

    const SHIP_ID    = GEAR_SLOT.id;
    const SHIP_KAI   = GEAR_SLOT.kai;
    const SHIP_TYPE  = GEAR_SLOT.type;
    const EQUIP_SLOT = GEAR_SLOT.slot;

    let gear_type    = [];
    const EQUIPABLE  = EQUIP.find(idx => idx.id === SHIP_ID);
    if (EQUIPABLE)
    {
      let current_break = null;
      let current_slot  = null;

      if (SHIP_KAI) current_break  = EQUIPABLE.retrofit;
      if (!SHIP_KAI) current_break = EQUIPABLE.break_4;

      if (EQUIP_SLOT === 1) current_slot = current_break.equip_1;
      if (EQUIP_SLOT === 2) current_slot = current_break.equip_2;
      if (EQUIP_SLOT === 3) current_slot = current_break.equip_3;
      if (EQUIP_SLOT === 4) current_slot = current_break.equip_4;
      if (EQUIP_SLOT === 5) current_slot = current_break.equip_5;

      current_slot.forEach((idx) =>
      {
        const EQUIP_TYPE = idx.type.split("/");
        EQUIP_TYPE.forEach((olx) => gear_type.push(olx));
      });
    };

    const EQUIPMENT_LIST = equipmentGet(GEAR, gear_type, SHIP_TYPE);

    let equip_slot;
    if (SLOT === 1) equip_slot = GEAR_DATA.slot1;
    if (SLOT === 2) equip_slot = GEAR_DATA.slot2;
    if (SLOT === 3) equip_slot = GEAR_DATA.slot3;

    const gearClose = (idx) =>
    {
      if (typeof idx === "string")
      {
        if (EQUIP_SLOT === 1) equip_slot.primary     = idx;
        if (EQUIP_SLOT === 2) equip_slot.secondary   = idx;
        if (EQUIP_SLOT === 3) equip_slot.tertiary    = idx;
        if (EQUIP_SLOT === 4) equip_slot.auxiliary_1 = idx;
        if (EQUIP_SLOT === 5) equip_slot.auxiliary_2 = idx;
      };
      this.props.gearClose({ ...equip_slot }, SLOT);
    };

    const eventClick = (idx) => gearClose(idx);

    return (
      <Modal isOpen={GEAR_OPEN} toggle={gearClose} size="xl" className="pb-5 dark">
        <ModalBody>
          <Table>
            <thead>
              <tr>
                <th>ICON</th>
                <th>NAME</th>
                <th>TYPE</th>
                <th>RELOAD</th>
              </tr>
            </thead>
            <tbody>
              {EQUIPMENT_LIST.map((api, key) =>
              {
                const RARITY  = api.highest_rarity;
                const GEAR_ID = api.id;

                let rarity_bg = "equipment_empty";
                if (RARITY === 5) rarity_bg = "equipment-rarity-5 m-auto";
                if (RARITY === 4) rarity_bg = "equipment-rarity-4 m-auto";
                if (RARITY === 3) rarity_bg = "equipment-rarity-3 m-auto";
                if (RARITY === 2) rarity_bg = "equipment-rarity-2 m-auto";
                if (RARITY === 1) rarity_bg = "equipment-rarity-1 m-auto";
                let reload = "0s";

                if (api.rarity === rarityGet(RARITY))
                  if (api.stats.rateOfFire) reload = api.stats.rateOfFire.max;

                return (
                  <tr key={key} onClick={() => eventClick(GEAR_ID)}>
                    <td><div className={rarity_bg}><ImgR src={api.image} /></div></td>
                    <td>{api.names.en}</td>
                    <td>{api.type.name}</td>
                    <td>{reload}</td>
                  </tr>
                );
              })}
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
    );
  };
};

EquipmentModal.propTypes = propTypes;
