import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

// Stats Icon
import statAntiAir     from "../../../assets/AL/AntiAir.png";
import statArmor       from "../../../assets/AL/Armor.png";
import statASW         from "../../../assets/AL/ASW.png";
import statAviation    from "../../../assets/AL/Aviation.png";
import statConsumption from "../../../assets/AL/Consumption.png";
import statEvasion     from "../../../assets/AL/Evasion.png";
import statFirepower   from "../../../assets/AL/Firepower.png";
import statHealth      from "../../../assets/AL/Health.png";
import statHit         from "../../../assets/AL/Hit.png";
import statLuck        from "../../../assets/AL/Luck.png";
import statReload      from "../../../assets/AL/Reload.png";
import statTorpedo     from "../../../assets/AL/Torpedo.png";

const propTypes = {
  retrofit: PropTypes.bool,
  baseStats: PropTypes.object,
  level100: PropTypes.object,
  level120: PropTypes.object,
  level125: PropTypes.object,
  level100Retrofit: PropTypes.object,
  level120Retrofit: PropTypes.object,
  level125Retrofit: PropTypes.object
};

export default class ShipStats extends Component
{
  render()
  {
    const RETROFIT = this.props.retrofit;

    // Stats child => (type: string)
    // health, armor, reload, luck, firepower, torpedo, evasion,
    // speed, antiair, aviation, oilConsumption, accuracy, antisubmarineWarfare
    const BASE_STATS = this.props.baseStats;
    const LEVEL_100  = this.props.level100;
    const LEVEL_120  = this.props.level120;
    const LEVEL_125  = this.props.level125;

    const LEVEL_100_KAI = this.props.level100Retrofit;
    const LEVEL_120_KAI = this.props.level120Retrofit;
    const LEVEL_125_KAI = this.props.level125Retrofit;

    let [
      stats_kai, health_kai, reload_kai, luck_kai, firepower_kai,
      torpedo_kai, evasion_kai, speed_kai, antiair_kai, aviation_kai,
      oil_kai, accuracy_kai, asw_kai
    ] = Array(13).fill(null);

    let shipArmor = (<th colSpan="4">{BASE_STATS.armor}</th>);

    if (RETROFIT)
    {
      const kai = (type) =>
      {
        return (<><th>{LEVEL_100_KAI[type]}</th><th>{LEVEL_120_KAI[type]}</th><th>{LEVEL_125_KAI[type]}</th></>);
      };

      stats_kai     = (<><th>100R</th><th>120R</th><th>125R</th></>);
      health_kai    = kai("health");
      reload_kai    = kai("reload");
      luck_kai      = kai("luck");
      firepower_kai = kai("firepower");
      torpedo_kai   = kai("torpedo");
      evasion_kai   = kai("evasion");
      speed_kai     = kai("speed");
      antiair_kai   = kai("antiair");
      aviation_kai  = kai("aviation");
      oil_kai       = kai("oilConsumption");
      accuracy_kai  = kai("accuracy");
      asw_kai       = kai("antisubmarineWarfare");

      if (BASE_STATS.armor === LEVEL_100_KAI.armor) shipArmor = (<th colSpan="7">{BASE_STATS.armor}</th>);
      else shipArmor = (<><th colSpan="4">{BASE_STATS.armor}</th><th colSpan="3">{LEVEL_100_KAI.armor}</th></>);
    };

    const statsFormat = (type) =>
    {
      return (
        <>
          <th>{BASE_STATS[type]}</th>
          <th>{LEVEL_100[type]}</th>
          <th>{LEVEL_120[type]}</th>
          <th>{LEVEL_125[type]}</th>
        </>
      );
    };

    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th>Stats</th>
              <th>Base</th>
              <th>100</th>
              <th>120</th>
              <th>125</th>
              {stats_kai}
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>
                <img src={statHealth} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("health")}
              {health_kai}
            </tr>
            <tr>
              <th>
                <img src={statArmor} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {shipArmor}
            </tr>
            <tr>
              <th>
                <img src={statReload} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("reload")}
              {reload_kai}
            </tr>
            <tr>
              <th>
                <img src={statLuck} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("luck")}
              {luck_kai}
            </tr>
            <tr>
              <th>
                <img src={statFirepower} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("firepower")}
              {firepower_kai}
            </tr>
            <tr>
              <th>
                <img src={statTorpedo} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("torpedo")}
              {torpedo_kai}
            </tr>
            <tr>
              <th>
                <img src={statEvasion} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("evasion")}
              {evasion_kai}
            </tr>
            <tr>
              <th>SPD</th>
              {statsFormat("speed")}
              {speed_kai}
            </tr>
            <tr>
              <th>
                <img src={statAntiAir} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("antiair")}
              {antiair_kai}
            </tr>
            <tr>
              <th>
                <img src={statAviation} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("aviation")}
              {aviation_kai}
            </tr>
            <tr>
              <th>
                <img src={statConsumption} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("oilConsumption")}
              {oil_kai}
            </tr>
            <tr>
              <th>
                <img src={statHit} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("accuracy")}
              {accuracy_kai}
            </tr>
            <tr>
              <th>
                <img src={statASW} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {statsFormat("antisubmarineWarfare")}
              {asw_kai}
            </tr>
          </tbody>
        </Table>
      </div>
    );
  };
};

ShipStats.propTypes = propTypes;
