import React from "react";
import { Link } from "react-router-dom";
// icon load
import "../../assets/css/nucleo-icons.css";
// reactstrap components
import {
  Button,
  // NavItem,
  // NavLink,
  // Nav,
  Container,
  // Row,
  // Col,
  UncontrolledTooltip
} from "reactstrap";

export default function Footer()
{
  return (
    <footer className="footer">
      <Container className="align-items-center text-center">
        <div className="btn-wrapper profile">
          <Button
            className="btn-icon btn-neutral btn-round btn-simple"
            color="default"
            tag={Link}
            to="/"
            id="tooltip0001"
            target="_blank"
          >
            <i className="tim-icons tim-icons-lg icon-atom" />
          </Button>
          <UncontrolledTooltip delay={0} target="tooltip0001">
            Home
          </UncontrolledTooltip>
          <Button
            className="btn-icon btn-neutral btn-round btn-simple"
            color="default"
            href="https://twitter.com/Fernando260301"
            id="tooltip622135962"
            target="_blank"
          >
            <i className="fab fa-twitter" />
          </Button>
          <UncontrolledTooltip delay={0} target="tooltip622135962">
            Twitter
          </UncontrolledTooltip>
          <Button
            className="btn-icon btn-neutral btn-round btn-simple"
            color="default"
            href="https://azurlane.koumakan.jp"
            id="tooltip230450801"
            target="_blank"
          >
            <i className="tim-icons icon-planet" />
          </Button>
          <UncontrolledTooltip delay={0} target="tooltip230450801">
            Azur Lane Wiki
          </UncontrolledTooltip>
        </div>
      </Container>
    </footer>
  );
};
