import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Table } from "reactstrap";

// Core Components
import factionClass from "../../../components/factionClass.js";

const propTypes = {
  navigate: PropTypes.any,
  equipmentList: PropTypes.array
};

export default class Mobile extends Component
{
  render()
  {
    let navigate = this.props.navigate;
    const EQUIPMENT_LIST = this.props.equipmentList;

    return (
      <Table hover className="mobile-view">
        <thead>
          <tr>
            <th>Icon</th>
            <th>Name</th>
            <th>Faction</th>
          </tr>
        </thead>
        <tbody>
          {EQUIPMENT_LIST.map((equip, key) =>
          {
            const EQUIPMENT_ID      = equip.id;
            const EQUIPMENT_NAME    = equip.names.en;
            const EQUIPMENT_IMAGE   = equip.image;
            const EQUIPMENT_FACTION = equip.nationality;
            const EQUIPMENT_RARITY  = equip.highest_rarity;
            const FACTION_CLASS     = factionClass(EQUIPMENT_FACTION);
            const EQUIPMENT_LINK    = encodeURIComponent(EQUIPMENT_ID.replace(/\s+/g, "_"));
            return (
              <tr key={key} onClick={() => navigate(EQUIPMENT_LINK)}>
                <td className={"rarity-" + EQUIPMENT_RARITY}>
                  <img
                    src={EQUIPMENT_IMAGE}
                    referrerPolicy="no-referrer"
                    alt="..."
                    className="img-fluid equipmentIcon"
                  />
                </td>
                <td>
                  <Link to={`/equipment/${EQUIPMENT_LINK}`} className="linkCS">
                    {EQUIPMENT_NAME}
                  </Link>
                </td>
                <td className={FACTION_CLASS + " faction-background"}>{EQUIPMENT_FACTION}</td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    );
  };
};

Mobile.propTypes = propTypes;
