import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

import typeAE  from "../../../assets/AL/AE.png";
import typeAR  from "../../../assets/AL/AR.png";
import typeBB  from "../../../assets/AL/BB.png";
import typeBBV from "../../../assets/AL/BBV.png";
import typeBC  from "../../../assets/AL/BC.png";
import typeBM  from "../../../assets/AL/BM.png";
import typeCA  from "../../../assets/AL/CA.png";
import typeCB  from "../../../assets/AL/CB.png";
import typeCL  from "../../../assets/AL/CL.png";
import typeCV  from "../../../assets/AL/CV.png";
import typeCVL from "../../../assets/AL/CVL.png";
import typeDD  from "../../../assets/AL/DD.png";
import typeDDG from "../../../assets/AL/DDG.png";
import typeSS  from "../../../assets/AL/SS.png";
import typeSSV from "../../../assets/AL/SSV.png";

import Health    from "../../../assets/AL/Health.png";
import Firepower from "../../../assets/AL/Firepower.png";
import Torpedo   from "../../../assets/AL/Torpedo.png";
import AntiAir   from "../../../assets/AL/AntiAir.png";
import Reload    from "../../../assets/AL/Reload.png";
import Accuracy  from "../../../assets/AL/Hit.png";
import Evasion   from "../../../assets/AL/Evasion.png";
import ASW       from "../../../assets/AL/ASW.png";

import TechPoint from "../../../assets/AL/TechPoint.png";

const propTypes = { fleetTech: PropTypes.object };

export default class ShipTech extends Component
{
  render()
  {
    const FLEET_TECH = this.props.fleetTech;

    // statsBonus => null || {applicable(multiple array[]), bonus, stat} (type: string or null)
    const COLLECTION = FLEET_TECH.statsBonus.collection;
    const MAX_LEVEL  = FLEET_TECH.statsBonus.maxLevel;

    // techPoints => {collection, maxLimitBreak, maxLevel, total} (type: number ? null = 0)
    const TECH_POINTS = FLEET_TECH.techPoints;

    let collection_format = null;
    let max_level_format  = null;

    if (COLLECTION)
    {
      const APPLICABLE = COLLECTION.applicable;
      const BONUS = COLLECTION.bonus;
      const STAT  = COLLECTION.stat;

      let stat_type = null;
      if (STAT === "health")
        stat_type = Health;

      if (STAT === "firepower")
        stat_type = Firepower;

      if (STAT === "torpedo")
        stat_type = Torpedo;

      if (STAT === "antiair")
        stat_type = AntiAir;

      if (STAT === "reload")
        stat_type = Reload;

      if (STAT === "accuracy")
        stat_type = Accuracy;

      if (STAT === "evasion")
        stat_type = Evasion;

      if (STAT === "antisubmarineWarfare")
        stat_type = ASW;

      const STAT_FORMAT = (<img src={stat_type} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);

      collection_format = (
        <>
          {APPLICABLE.map((type, key) =>
          {
            let hull_type = null;
            if (type === "Battleship")
              hull_type = typeBB;

            if (type === "Battlecruiser")
              hull_type = typeBC;

            if (type === "Aviation battleship")
              hull_type = typeBBV;

            if (type === "Destroyer")
              hull_type = typeDD;

            if (type === "Guided-missile destroyer")
              hull_type = typeDDG;

            if (type === "Heavy cruiser")
              hull_type = typeCA;

            if (type === "Large cruiser")
              hull_type = typeCB;

            if (type === "Light cruiser")
              hull_type = typeCL;

            if (type === "Monitor")
              hull_type = typeBM;

            if (type === "Aircraft carrier")
              hull_type = typeCV;

            if (type === "Light aircraft carrier")
              hull_type = typeCVL;

            if (type === "Submarine")
              hull_type = typeSS;

            if (type === "Submarine carrier")
              hull_type = typeSSV;

            if (type === "Munition ship")
              hull_type = typeAE;

            if (type === "Repair ship")
              hull_type = typeAR;

            return (<img key={key} src={hull_type} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
          })}
          {STAT_FORMAT}
          {BONUS}
        </>
      );
    };

    if (MAX_LEVEL)
    {
      const APPLICABLE = MAX_LEVEL.applicable;
      const BONUS = MAX_LEVEL.bonus;
      const STAT  = MAX_LEVEL.stat;

      let stat_type = null;
      if (STAT === "health")
        stat_type = Health;

      if (STAT === "firepower")
        stat_type = Firepower;

      if (STAT === "torpedo")
        stat_type = Torpedo;

      if (STAT === "antiair")
        stat_type = AntiAir;

      if (STAT === "reload")
        stat_type = Reload;

      if (STAT === "accuracy")
        stat_type = Accuracy;

      if (STAT === "evasion")
        stat_type = Evasion;

      if (STAT === "antisubmarineWarfare")
        stat_type = ASW;

      const STAT_FORMAT = (<img src={stat_type} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
      max_level_format = (
        <>
          {APPLICABLE.map((type, key) =>
          {
            let hull_type = null;
            if (type === "Battleship")
              hull_type = typeBB;

            if (type === "Battlecruiser")
              hull_type = typeBC;

            if (type === "Aviation battleship")
              hull_type = typeBBV;

            if (type === "Destroyer")
              hull_type = typeDD;

            if (type === "Guided-missile destroyer")
              hull_type = typeDDG;

            if (type === "Heavy cruiser")
              hull_type = typeCA;

            if (type === "Large cruiser")
              hull_type = typeCB;

            if (type === "Light cruiser")
              hull_type = typeCL;

            if (type === "Monitor")
              hull_type = typeBM;

            if (type === "Aircraft carrier")
              hull_type = typeCV;

            if (type === "Light aircraft carrier")
              hull_type = typeCVL;

            if (type === "Submarine")
              hull_type = typeSS;

            if (type === "Submarine carrier")
              hull_type = typeSSV;

            if (type === "Munition ship")
              hull_type = typeAE;

            if (type === "Repair ship")
              hull_type = typeAR;

            return (<img key={key} src={hull_type} referrerPolicy="no-referrer" alt="..." className="img-fluid" />);
          })}
          {STAT_FORMAT}
          {BONUS}
        </>
      );
    };

    const TECH_POINTS_COLLECTION      = TECH_POINTS.collection ? TECH_POINTS.collection : 0;
    const TECH_POINTS_MAX_LIMIT_BREAK = TECH_POINTS.maxLimitBreak;
    const TECH_POINTS_MAX_LEVEL       = TECH_POINTS.maxLevel;
    const TECH_POINTS_TOTAL           = TECH_POINTS.total;
    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th colSpan="3">Fleet Technology</th>
            </tr>
            <tr>
              <th>Unlock Conditions</th>
              <th>Stat Bonuses</th>
              <th>Tech Points</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>Collection</th>
              <th>{collection_format}</th>
              <th>
                {TECH_POINTS_COLLECTION}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th>Max Limit Break</th>
              <th/>
              <th>
                {TECH_POINTS_MAX_LIMIT_BREAK}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th>Level 120</th>
              <th>{max_level_format}</th>
              <th>
                {TECH_POINTS_MAX_LEVEL}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
            <tr>
              <th colSpan="2">Total</th>
              <th>
                {TECH_POINTS_TOTAL}
                <img src={TechPoint} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
            </tr>
          </tbody>
        </Table>
      </div>
    );
  };
};

ShipTech.propTypes = propTypes;
