import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Coefficient extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-coefficient">Coef</th>
        <UncontrolledTooltip placement="top" target="templateHead-coefficient" delay={0}><b>Coefficient</b></UncontrolledTooltip>
      </>
    );
  };
};
