import React, { Component } from "react";
import PropTypes from "prop-types";

import Animation from "./EquipmentMisc/Animation.js";
import Notes     from "./EquipmentMisc/Notes.js";
import Obtained  from "./EquipmentMisc/Obtained.js";

const propTypes = { misc: PropTypes.object };

export default class EquipmentMisc extends Component
{
  render()
  {
    const equipment_misc  = this.props.misc;
    const animation_gif   = equipment_misc.animation;
    const equipment_notes = equipment_misc.notes;
    const obtained_from   = equipment_misc.obtainedFrom;

    return (
      <>
        <Animation animation={animation_gif} />
        <Notes notes={equipment_notes} />
        <Obtained obtainedFrom={obtained_from} />
      </>
    );
  };
};

EquipmentMisc.propTypes = propTypes;
