import React from "react";
import { Link } from "react-router-dom";

// icon load
import "../../assets/css/nucleo-icons.css";

// reactstrap components
import {
  Collapse,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col,
  UncontrolledTooltip
} from "reactstrap";

export default function Navbars()
{
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [collapseOut, setCollapseOut] = React.useState("");
  const [color, setColor] = React.useState("navbar-transparent");

  const changeColor = () =>
  {
    if (
      document.documentElement.scrollTop > 99 ||
      document.body.scrollTop > 99
    )
    {
      setColor("bg-info");
    }
    else if (
      document.documentElement.scrollTop < 100 ||
      document.body.scrollTop < 100
    )
    {
      setColor("navbar-transparent");
    }
  };

  React.useEffect(() =>
  {
    window.addEventListener("scroll", changeColor);
    return function cleanup()
    {
      window.removeEventListener("scroll", changeColor);
    };
  }, []);

  const toggleCollapse = () =>
  {
    document.documentElement.classList.toggle("nav-open");
    setCollapseOpen(!collapseOpen);
  };

  const onCollapseExiting = () => setCollapseOut("collapsing-out");

  const onCollapseExited = () => setCollapseOut("");

  return (
    <Navbar className={"fixed-top " + color} color-on-scroll="100" expand="lg">
      <Container>
        <div className="navbar-translate">
          <NavbarBrand to="/" id="navbar-brand" tag={Link}>
            <i className="tim-icons tim-icons-lg icon-atom" />
            <span> CS • </span>
            React
          </NavbarBrand>
          <UncontrolledTooltip placement="bottom" target="navbar-brand">
            celestialsparkz.github.io
          </UncontrolledTooltip>
          <button
            aria-expanded={collapseOpen}
            className="navbar-toggler navbar-toggler"
            onClick={toggleCollapse}
          >
            <span className="navbar-toggler-bar bar1" />
            <span className="navbar-toggler-bar bar2" />
            <span className="navbar-toggler-bar bar3" />
          </button>
        </div>
        <Collapse
          className={"justify-content-end " + collapseOut}
          navbar
          isOpen={collapseOpen}
          onExiting={onCollapseExiting}
          onExited={onCollapseExited}
        >
          <div className="navbar-collapse-header">
            <Row>
              <Col className="collapse-brand" xs="6">
                <a href="#pablo" onClick={(e) => e.preventDefault()}>
                  CS • React
                </a>
              </Col>
              <Col className="collapse-close text-right" xs="6">
                <button
                  aria-expanded={collapseOpen}
                  className="navbar-toggler"
                  onClick={toggleCollapse}
                >
                  <i className="tim-icons icon-simple-remove" />
                </button>
              </Col>
            </Row>
          </div>
          <Nav navbar>
            <NavItem>
              <NavLink tag={Link} to="/ship">
                Ship
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/meowfficer">
                Meowfficer
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/equipment">
                Equipment
              </NavLink>
            </NavItem>
            <NavItem className="p-0">
              <NavLink
                data-placement="bottom"
                href="https://twitter.com/Fernando260301"
                rel="noopener noreferrer"
                target="_blank"
                title="Twitter"
              >
                <i className="fab fa-twitter" />
                <p className="d-lg-none d-xl-none">Twitter</p>
              </NavLink>
            </NavItem>
            <NavItem className="p-0">
              <NavLink
                data-placement="bottom"
                href="https://azurlane.koumakan.jp"
                rel="noopener noreferrer"
                target="_blank"
                title="Azur Lane Wiki"
              >
                <i className="tim-icons icon-planet" />
                <p className="d-lg-none d-xl-none">Azur Lane Wiki</p>
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
};
