import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class VolleyTime extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-volleytime">Vt</th>
        <UncontrolledTooltip placement="top" target="templateHead-volleytime" delay={0}><b>Volley Time</b></UncontrolledTooltip>
      </>
    );
  };
};
