import React, { Component } from "react";
import PropTypes from "prop-types";

import ShipProfile from "./ShipProfile.js";
import ShipFetch   from "./ShipFetch.js";

const propTypes = {
  shipData: PropTypes.any,
  bannerData: PropTypes.any,
  paramsId: PropTypes.string
};

export default class ShipRoute extends Component
{
  render()
  {
  	const SHIP_DATA   = this.props.shipData;
    const BANNER_DATA = this.props.bannerData;
    const PARAMS_ID   = this.props.paramsId;
  	if (SHIP_DATA === undefined)
  	  return <ShipFetch paramsId={PARAMS_ID} />;

    return <ShipProfile shipData={SHIP_DATA} bannerData={BANNER_DATA} />;
  };
};

ShipRoute.propTypes = propTypes;
