import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class PreloadHeavy extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-preloadheavy" className="dps-ph">PH</th>
        <UncontrolledTooltip placement="top" target="templateHead-preloadheavy" delay={0}><b>Preload DPS vs Heavy Armor</b></UncontrolledTooltip>
      </>
    );
  };
};
