import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class CrashDamage extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-crashdamage">Crash DMG</th>
        <UncontrolledTooltip placement="top" target="templateHead-crashdamage" delay={0}><b>Crash Damage</b></UncontrolledTooltip>
      </>
    );
  };
};
