export default function ordnance(unit)
{
  let light        = 0;
  let medium       = 0;
  let heavy        = 0;
  let spread       = 0;
  let splash_range = 0;
  let damage       = 0;

  if (unit.includes("100lb Bomb"))
  {
    light  = 80;
    medium = 85;
    heavy  = 100;
    spread = 33;
    splash_range = 16;
    if (unit.includes("T2")) damage = 159;
    if (unit.includes("T3")) damage = 173;
    if (unit.includes("T0")) damage = 185;
  };

  if (unit.includes("500lb Bomb"))
  {
    light  = 80;
    medium = 90;
    heavy  = 110;
    spread = 36;
    splash_range = 19;
    if (unit.includes("T2")) damage = 330;
    if (unit.includes("T3")) damage = 360;
    if (unit.includes("T0")) damage = 380;
  };

  if (unit.includes("1000lb Bomb"))
  {
    light  = 80;
    medium = 95;
    heavy  = 115;
    spread = 40;
    splash_range = 22;
    if (unit.includes("T2")) damage = 369;
    if (unit.includes("T3")) damage = 402;
    if (unit.includes("T0")) damage = 416;
  };

  if (unit.includes("1000lb BC Bomb"))
  {
    light  = 80;
    medium = 95;
    heavy  = 115;
    spread = 40;
    splash_range = 22;
    damage  = 440;
  };

  if (unit.includes("1600lb Bomb"))
  {
    light  = 75;
    medium = 100;
    heavy  = 120;
    spread = 40;
    splash_range = 25;
    if (unit.includes("T2")) damage = 393;
    if (unit.includes("T3")) damage = 429;
  };

  if (unit.includes("1600lb AP Bomb"))
  {
    light  = 80;
    medium = 110;
    heavy  = 130;
    spread = 40;
    splash_range = 25;
    damage  = 429;
  };

  if (unit.includes("800kg AP Bomb"))
  {
    light  = 80;
    medium = 110;
    heavy  = 130;
    spread = 40;
    splash_range = 25;
    damage  = 432;
  };

  if (unit.includes("2000lb Bomb"))
  {
    light  = 70;
    medium = 105;
    heavy  = 125;
    spread = 42;
    splash_range = 28;
    if (unit.includes("T2")) damage = 417;
    if (unit.includes("T3")) damage = 456;
    if (unit.includes("T0")) damage = 480;
  };

  if (unit.includes("AP Rocket"))
  {
    light  = 110;
    medium = 140;
    heavy  = 50;
    spread = "12 x 5";
    splash_range = 7;
    damage  = 160;
  };

  return ({
    light: light,
    medium: medium,
    heavy: heavy,
    splash_range: splash_range,
    spread: spread,
    damage: damage
  });
};
