import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = { slots: PropTypes.array };

export default class ShipSlots extends Component
{
  render()
  {
    // Slots => {multiple array[]} (type: 4 number, 1 string)
    // Slots[array] => {maxEfficiency, minEfficiency, type, max, kaiEfficiency} (type: 4 number, 1 string)
    const SLOTS = this.props.slots;

    return (
      <div>
        <Table className="tableCS shipSlots">
          <thead>
            <tr>
              <th colSpan="3">Equipment Slot</th>
            </tr>
            <tr>
              <th>Equipment</th>
              <th>Efficiency</th>
              <th>Mount</th>
            </tr>
          </thead>
          <tbody>
            {SLOTS.map((slot, key) =>
            {
              const MIN_EFFICIENCY = slot.minEfficiency + "%";
              const MAX_EFFICIENCY = slot.maxEfficiency + "%";

              let kai_efficiency   = "";
              if (slot.kaiEfficiency) kai_efficiency = " → " + slot.kaiEfficiency + "%";

              const SLOT_TYPE       = slot.type;
              const SLOT_EFFICIENCY = MIN_EFFICIENCY + " → " + MAX_EFFICIENCY + kai_efficiency;
              const SLOT_MAX        = slot.max;
              return (
                <tr key={key}>
                  <th>{SLOT_TYPE}</th>
                  <th>{SLOT_EFFICIENCY}</th>
                  <th>{SLOT_MAX}</th>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    );
  };
};

ShipSlots.propTypes = propTypes;
