import React, { Component } from "react";
import PropTypes from "prop-types";

import MeowfficerProfile     from "./MeowfficerModal/MeowfficerProfile";
import MeowfficerSkill       from "./MeowfficerModal/MeowfficerSkill";
import MeowfficerTalentModal from "./MeowfficerModal/MeowfficerTalentModal";
import MeowfficerData        from "../../assets/json/MeowfficerData.json";
// import MeowfficerTalent from "assets/json/MeowfficerTalent.json"

const propTypes = {
  modalData: PropTypes.object,
  modalOpen: PropTypes.bool,
  modalClose: PropTypes.func,
  openSkillModal: PropTypes.func,
  skillModalOpen: PropTypes.bool,
  skillModalClose: PropTypes.func,
  talentData: PropTypes.object,
  openTalentModal: PropTypes.func,
  talentModalOpen: PropTypes.bool,
  talentModalClose: PropTypes.func
};

export default class MeowfficerModal extends Component
{
  render()
  {
    const MODAL_DATA         = this.props.modalData;
    const MODAL_OPEN         = this.props.modalOpen;
    const MODAL_CLOSE        = this.props.modalClose;

    const OPEN_SKILL_MODAL   = this.props.openSkillModal;
    const SKILL_MODAL_OPEN   = this.props.skillModalOpen;
    const SKILL_MODAL_CLOSE  = this.props.skillModalClose;

    const TALENT_DATA        = this.props.talentData;
    const OPEN_TALENT_MODAL  = this.props.openTalentModal;
    const TALENT_MODAL_OPEN  = this.props.talentModalOpen;
    const TALENT_MODAL_CLOSE = this.props.talentModalClose;

    let idx = MODAL_DATA.codex;
    if (idx === null || idx === undefined) idx = 0;

    const MEOWFFICER = MeowfficerData[idx];

    // profile
    const BANNER     = MEOWFFICER.banner;
    const NAME       = MEOWFFICER.name;
    const LOGISTICS  = MEOWFFICER.stats.logistics[0] + " → " + MEOWFFICER.stats.logistics[1];
    const DIRECTIVES = MEOWFFICER.stats.directives[0] + " → " + MEOWFFICER.stats.directives[1];
    const TACTICS    = MEOWFFICER.stats.tactics[0] + " → " + MEOWFFICER.stats.tactics[1];
    const TYPE       = MEOWFFICER.type;
    const SLOT       = MEOWFFICER.slot;
    const RARITY     = MEOWFFICER.rarity;
    const FACTION    = MEOWFFICER.faction;
    const BUILD_TIME = MEOWFFICER.timer;

    // skill
    const SKILL_NAME  = MEOWFFICER.skill.name;
    const SKILL_IMAGE = MEOWFFICER.skill.image;

    const SKILL_LEVEL_1 = MEOWFFICER.skill.general.level1;
    const SKILL_LEVEL_2 = MEOWFFICER.skill.general.level2;
    const SKILL_LEVEL_3 = MEOWFFICER.skill.general.level3;

    const SKILL_OPSI_1 = MEOWFFICER.skill.opsi.level1;
    const SKILL_OPSI_2 = MEOWFFICER.skill.opsi.level2;
    const SKILL_OPSI_3 = MEOWFFICER.skill.opsi.level3;

    // talent
    const TALENT_TYPE      = TALENT_DATA.talentType;
    const TALENT_PRIMARY   = TALENT_DATA.primary;
    const TALENT_SECONDARY = TALENT_DATA.secondary;
    const TALENT_OTHER     = TALENT_DATA.other;

    // profile parse
    const PROFILE = ({
      banner: BANNER,
      name: NAME,
      logistics: LOGISTICS,
      directives: DIRECTIVES,
      tactics: TACTICS,
      type: TYPE,
      slot: SLOT,
      rarity: RARITY,
      faction: FACTION,
      timer: BUILD_TIME,
      skill: SKILL_NAME
    });

    // skill parse
    const SKILL = ({
      name: SKILL_NAME,
      image: SKILL_IMAGE,
      general: { level1: SKILL_LEVEL_1, level2: SKILL_LEVEL_2, level3: SKILL_LEVEL_3 },
      opsi: { level1: SKILL_OPSI_1, level2: SKILL_OPSI_2, level3: SKILL_OPSI_3 }
    });

    // talent parse
    const TALENT = ({
      talentType: TALENT_TYPE,
      primary: TALENT_PRIMARY,
      secondary: TALENT_SECONDARY,
      other: TALENT_OTHER
    });

    return (
      <>
        <MeowfficerProfile
          modalOpen={MODAL_OPEN}
          modalClose={MODAL_CLOSE}
          openSkillModal={OPEN_SKILL_MODAL}
          openTalentModal={OPEN_TALENT_MODAL}
          profile={PROFILE}
        />
        <MeowfficerSkill
          skillModalOpen={SKILL_MODAL_OPEN}
          skillModalClose={SKILL_MODAL_CLOSE}
          skill={SKILL}
        />
        <MeowfficerTalentModal
          talentModalOpen={TALENT_MODAL_OPEN}
          talentModalClose={TALENT_MODAL_CLOSE}
          talent={TALENT}
        />
      </>
    );
  };
};

MeowfficerModal.propTypes = propTypes;
