import React, { Component } from "react";
import { UncontrolledTooltip } from "reactstrap";

export default class Stars extends Component
{
  render()
  {
    return (
      <>
        <th id="templateHead-stars">★</th>
        <UncontrolledTooltip placement="top" target="templateHead-stars" delay={0}><b>Stars</b></UncontrolledTooltip>
      </>
    );
  };
};
