import React, { Component } from "react";
import PropTypes from "prop-types";
import { Button, Modal, ModalBody, Table } from "reactstrap";

import MeowfficerTalentList from "../../assets/json/MeowfficerTalentList.json";

const propTypes = {
  openAllTalent: PropTypes.func,
  allTalentOpen: PropTypes.bool,
  allTalentClose: PropTypes.func
};

export default class MeowfficerAllTalentModal extends Component
{
  render()
  {
    const OPEN_ALL_TALENT  = this.props.openAllTalent;
    const ALL_TALENT_OPEN  = this.props.allTalentOpen;
    const ALL_TALENT_CLOSE = this.props.allTalentClose;
    return (
      <>
	      <Button color="primary" block onClick={OPEN_ALL_TALENT}>Talent List</Button>
        <Modal isOpen={ALL_TALENT_OPEN} toggle={ALL_TALENT_CLOSE} size="xl" className="MeowfficerAllTalentModal">
          <ModalBody>
            <Table bordered className="catModalTable">
              <thead>
                <tr>
                  <th colSpan="2">All Talent List</th>
                </tr>
              </thead>
              <tbody>
                {MeowfficerTalentList.map((talent, key) =>
                {
                  const NAME = talent.name.format;
                  const DESC = talent.desc.format;
                  const IDX  = key;
                  const IDS  = talent.id;
                  const KEYS = "talent" + key + NAME;
                  return (
                    <tr key={KEYS}>
                      <th>
                        {MeowfficerTalentList[IDX].image.map((image, imgkey) =>
                        {
                          const IMG_KEYS = "TalentImage" + imgkey + IDS;
                          return (
                            <img
                              key={IMG_KEYS}
                              className="img-fluid talentIcon"
                              referrerPolicy="no-referrer"
                              alt="..."
                              src={image}
                            />
                          );
                        })}
                        <br/>{NAME}
                      </th>
                      <th>{DESC}</th>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          </ModalBody>
        </Modal>
      </>
  	);
  };
};

MeowfficerAllTalentModal.propTypes = propTypes;
